interface Point {
  x: number
  y: number
}

/**
 * 检测点是否在线段上
 * @param point 检测点
 * @param segments 端点集合
 * @param tolerance 容差
 * @returns
 */
export const isPointOnLineSegments = (
  point: Point,
  segments: Point[],
  tolerance = 0,
): boolean | number => {
  const { x, y } = point
  const xTolerance = tolerance || 0
  const yTolerance = tolerance || 0
  for (let i = 0; i < segments.length - 1; i++) {
    const p1 = segments[i]
    const p2 = segments[i + 1]
    const a = p1.y - p2.y
    const b = p2.x - p1.x
    const c = p1.x * p2.y - p2.x * p1.y
    const distance = Math.abs(a * x + b * y + c) / Math.sqrt(a * a + b * b)
    if (
      distance <= xTolerance &&
      x >= Math.min(p1.x, p2.x) - xTolerance &&
      x <= Math.max(p1.x, p2.x) + xTolerance &&
      y >= Math.min(p1.y, p2.y) - yTolerance &&
      y <= Math.max(p1.y, p2.y) + yTolerance
    ) {
      return i
    }
  }
  return false
}
