import { EventCenter } from '../core/shared/event'
import { LockIcon } from '../core/materials/icon/index'
import { Canvas, BackgroundType } from '../core/shared/canvas'
import { mathRange } from '../core/utils/math'
import { nextTick } from '../core/utils/dom'
import type { CanvasEvent, Id } from '../types/index'

import { DataHandler } from './hooks/data-handler'
import { NodeErwin as Node } from '../core/materials/node'
import { NodeRect } from '../core/materials/node'
import { LineErwin } from '../core/materials/line/line-erwin'
import { TextBase } from '../core/materials/text'
import { FrameResize, FrameHover } from '../core/materials/frame'
import { useMouseMove, useMouseDown, useMouseUp } from './hooks/event'

import { EditType, TargetDirection, Cursor } from './shared/enum'
import { Plugins, PluginsEnum } from '../plugins'

import { cloneDeep, isNumber, pick, isBoolean } from 'lodash-es'
import { Editable } from '../types/enum'

import { preloadLineageImages } from '../core/shared/image'

import { hierarchicalLayout } from './hooks/layout'
import {
  GraphEvent,
  NodeEvent,
  LineEvent,
  MarkEvent,
  Contextmenu,
} from './shared/event'

enum Color {
  primary = 'blue',
  border = '#000000',
}

interface RelatedLineItem {
  from: Set<LineErwin>
  to: Set<LineErwin>
}
export class Erg extends EventCenter {
  private el: HTMLElement
  public canvas: Canvas
  private cacheCtx: CanvasRenderingContext2D

  public dataHandler: DataHandler
  public nodeMap: Map<Id, Node>
  public lineMap: Map<Id, LineErwin>
  public noteMap: Map<Id, TextBase>
  public markMap: Map<Id, NodeRect>
  public nodeRelatedLineMap: Map<Node['id'], RelatedLineItem>

  // 优化：缓存页面渲染元素，避免在交互时循环全部数据
  public renderElements: Array<Node | LineErwin | TextBase | NodeRect>

  public externalFn: Record<string, () => void>
  public editType: EditType
  public copyTarget: unknown

  // 缓存事件对象
  public eventObj = {
    target: null,
    // 节点
    targetDirection: TargetDirection.NONE,
    targetOffsetX: 0,
    targetOffsetY: 0,
    targetAttrId: null,
    // 连线
    targetLineStartPointIdx: 0,
    lastEvLoc: { x: 0, y: 0 },
  }

  private plugins: Plugins
  private useAttrFocus: boolean
  private useHistory: boolean
  private useThumbnail: boolean
  private useRightMenu: boolean
  private rightMenuConfig = []

  private fontFamily: string
  private chooseTargets = []
  private imgList = {}
  private editable = Editable.Edit

  // 节点默认配置
  public defaultWidth = 100
  public defaultHeight = 50

  public focusTarget = new Set<Node | LineErwin | NodeRect | TextBase>()
  public hoverTarget: Node | LineErwin | NodeRect | TextBase

  private expandAttrs = []

  private _bindEventMousedown: () => void
  private _bindEventMousemove: () => void
  private _bindEventMouseup: () => void
  private _bindEventResize: () => void
  private _bindEventWheel: () => void
  private _bindEventContextmenu: () => void
  private _bindEventUndo: () => void
  private _bindEventRedo: () => void

  constructor(options) {
    super()
    this.updateConfig(options)
    this.mountDataHandler(options)
    this.mountCanvas()
    this.mountCanvasEvents()
    this.mountPlugins()
    preloadLineageImages().then(() => this.update())
  }

  //初始化数据方法
  public updateConfig(options) {
    const {
      el,
      externalFn,
      editable,
      fontFamily,
      rightMenu,
      useRightMenu = false,
      useAttrFocus = false,
      useThumbnail = false,
      useHistory = true,
    } = options

    if (el) this.el = el
    if (editable) this.editable = editable || Editable.Edit
    if (fontFamily) this.fontFamily = fontFamily
    if (rightMenu) this.rightMenuConfig = rightMenu
    // 是否使用右键菜单
    if (isBoolean(useRightMenu)) this.useRightMenu = useRightMenu
    // 属性是否可点击
    if (isBoolean(useAttrFocus)) this.useAttrFocus = useAttrFocus
    if (isBoolean(useThumbnail)) this.useThumbnail = useThumbnail
    if (isBoolean(useHistory)) this.useHistory = useHistory

    //拷贝外部方法指针
    /**
     * options[addNode]: 创建实体通知方法
     *  @param val {Object}, 完整的信息
     *
     * options[delNode]: 删除实体通知方法
     *  @param id {Id}, 实体的id
     *
     * options[addLine]: 创建线通知方法
     *  @param val {Object}, 完整的信息
     *
     * options[delLine]: 删除线通知方法
     *  @param from {Id}, to {Id}, 线的唯一标识(from, to)
     *
     * options[addFilter]: 创建过滤器通知方法
     *  @param val {Object}, 完整的信息
     *
     * options[delFilter]: 删除过滤器通知方法
     *  @param id {Id}, 过滤器的id
     *
     * options[$message]: 删除过滤器通知方法
     *  @param val {Object}, 同el-message参数
     *
     * options[handleCopy]: 拷贝通知方法
     *  @param val {Object}, 拷贝的对象
     *  @param prev {Object}, 上一个拷贝对象
     *
     * options[handlePaste]: 粘贴通知方法
     *  @param val {Object}, 拷贝的对象
     */
    if (externalFn) this.externalFn = externalFn
  }

  private mountDataHandler(options) {
    this.dataHandler = new DataHandler({
      context: this,
      data: options.data,
    })
  }

  public destroy() {
    this.unmountPlugins()
    this.unmountCanvasEvents()
    this.canvas.destroy()
    this.dataHandler.destroy()
  }

  private mountCanvas() {
    this.canvas = new Canvas({
      mountEl: this.el,
      backgroundType: BackgroundType.Checker,
    })
    this.cacheCtx = this.canvas.cacheCtx
  }

  private mountPlugins() {
    this.plugins = new Plugins()

    // 初始化历史队列
    this.useHistory && this.plugins.use(PluginsEnum.History)

    // 初始化全景缩略图
    this.useThumbnail &&
      this.plugins.use(PluginsEnum.Thumbnail, {
        mountEl: this.canvas.boxEl,
        mainCanvas: this.canvas,
      })

    // 初始化右键菜单
    this.useRightMenu &&
      this.plugins.use(PluginsEnum.RightMenu, this.el, this.rightMenuConfig)
  }

  private unmountPlugins() {
    this.plugins.destroy()
  }

  private mountCanvasEvents() {
    this._bindEventMousedown = this.onMouseDown.bind(this)
    this._bindEventMousemove = this.onMouseMove.bind(this)
    this._bindEventMouseup = this.onMouseUp.bind(this)
    this._bindEventResize = this.onResize.bind(this)
    this._bindEventWheel = this.onWheel.bind(this)
    this._bindEventContextmenu = this.onContextmenu.bind(this)
    this._bindEventUndo = this.onUndo.bind(this)
    this._bindEventRedo = this.onRedo.bind(this)

    this.canvas.on('canvas:resize', this._bindEventResize)
    this.canvas.on('canvas:wheel', this._bindEventWheel)
    this.canvas.on('canvas:mouseup', this._bindEventMouseup)
    this.canvas.on('canvas:mousedown', this._bindEventMousedown)
    this.canvas.on('canvas:mousemove', this._bindEventMousemove)
    this.canvas.on('canvas:contextmenu', this._bindEventContextmenu)
    this.canvas.on('canvas:undo', this._bindEventUndo)
    this.canvas.on('canvas:redo', this._bindEventRedo)
  }

  private unmountCanvasEvents() {
    this.canvas.off('canvas:resize', this._bindEventResize)
    this.canvas.off('canvas:wheel', this._bindEventWheel)
    this.canvas.off('canvas:mouseup', this._bindEventMouseup)
    this.canvas.off('canvas:mousedown', this._bindEventMousedown)
    this.canvas.off('canvas:mousemove', this._bindEventMousemove)
    this.canvas.off('canvas:contextmenu', this._bindEventContextmenu)
    this.canvas.off('canvas:undo', this._bindEventUndo)
    this.canvas.off('canvas:redo', this._bindEventRedo)
  }

  private onUndo() {
    const action = this.plugins.get(PluginsEnum.History).undo()
    action?.reverse()
    if (!action) console.warn('撤无可撤')
  }

  private onRedo() {
    const action = this.plugins.get(PluginsEnum.History).redo()
    action?.reverse()
    if (!action) console.warn('恢无可恢')
  }

  private onMouseUp(e: MouseEvent, canvasEv: CanvasEvent) {
    const { resetMouseState } = useMouseUp.call(this, e, canvasEv)
    resetMouseState()
  }

  private onMouseDown(e: MouseEvent, canvasEv: CanvasEvent) {
    const { editType } = this
    const { execTask, setEventTarget } = useMouseDown.call(this, e, canvasEv)

    // 有指定的操作任务：执行任务（添加节点/添加连线/....）
    if (editType) return execTask()

    // 无指定的操作任务：查找事件触发对象
    setEventTarget(e)
  }

  private onMouseMove(e: MouseEvent, canvasEv: CanvasEvent) {
    const { canvas, eventObj } = this
    const { changeMouseCursor, moveCanvas, moveElement } = useMouseMove.call(
      this,
      e,
      canvasEv,
    )

    const isJustMove = !canvas.isMouseDown
    const isMoveCanvas = canvas.isMouseDown && !eventObj.target
    const isMoveElement = canvas.isMouseDown && eventObj.target

    if (isJustMove) return changeMouseCursor()
    if (isMoveCanvas) return moveCanvas()
    if (isMoveElement) {
      // 加入历史堆栈
      const copyFocusTarget = [...this.focusTarget] as unknown[]

      for (let i = 0; i < copyFocusTarget.length; i++) {
        const it = copyFocusTarget[i]
        const originObject = {
          target: it,
          originValue: {},
        }
        if (it instanceof Node) {
          const attrs = pick(it, 'x', 'y', 'width', 'height')
          originObject.originValue = cloneDeep(attrs)

          const relatedLines = this.nodeRelatedLineMap.get(it.id)
          copyFocusTarget.push(...relatedLines.from, ...relatedLines.to)
        }
        if (it instanceof LineErwin) {
          const attrs = pick(it, 'pos')
          originObject.originValue = cloneDeep(attrs)
        }

        copyFocusTarget[i] = originObject
      }
      this.plugins.get(PluginsEnum.History).push(() => {
        // 缓存move事件
        // const target = this.nodeMap.get(eventObj.target.id) || this.lineMap.get(eventObj.target.id)
        // Object.assign(this.eventObj, {
        //   target,
        //   targetDirection: eventObj.targetDirection,
        //   targetLineStartPointIdx: eventObj.targetLineStartPointIdx,
        //   lastEvLoc: eventObj.lastEvLoc,
        //   targetOffsetX: eventObj.targetOffsetX,
        //   targetOffsetY: eventObj.targetOffsetY,
        // })

        // this.focusTarget.clear()
        // copyFocusTarget.map(i => this.focusTarget.add(i))
        // moveElement()

        // 缓存当前状态
        // this.focusTarget.clear()
        // copyFocusTarget.map(i => this.focusTarget.add(i.target))
        // 逆向操作数据
        for (let i = 0; i < copyFocusTarget.length; i++) {
          const { target, originValue } = copyFocusTarget[i]
          target.updateOptions(originValue)
          if (target instanceof Node) this.emit(NodeEvent.Resize, target)
          if (target instanceof LineErwin) this.emit(LineEvent.Move, target)
        }
        this.update()
      })

      return moveElement()
    }
  }

  private onContextmenu(e: MouseEvent) {
    if (!this.useRightMenu) return
    const { offsetX, offsetY } = e
    const { offsetWidth, offsetHeight } = e.target as HTMLElement
    this.emit(Contextmenu.Show, {
      target: this.hoverTarget,
    })
    console.log(this.hoverTarget)
    this.plugins.get(PluginsEnum.RightMenu).show({
      top: offsetY,
      left: offsetX,
      innerWidth: offsetWidth,
      innerHeight: offsetHeight,
      menuConfig: this.rightMenuConfig,
    })
  }

  private onWheel(e: WheelEvent) {
    const { deltaY, deltaX, ctrlKey } = e
    // console.log(deltaY);
    const wheelConfirm = Math.abs(deltaY) > 2 || Math.abs(deltaX) > 2

    if (!wheelConfirm) return

    // 缩放
    if (ctrlKey) {
      const zoomRate = this.canvas.zoomRate
      // const step = deltaY > 0 ? 10 : -10
      const calcRatio = Math.floor(mathRange(zoomRate * 100 + deltaY, 10, 200))
      this.zoom(calcRatio)
    }
    // 平移
    else {
      const translate = this.canvas.getTranslate()
      this.canvas.setTranslate({
        x: translate.x - deltaX,
        y: translate.y - deltaY,
      } as MouseEvent)
      this.update()
    }
  }

  private onResize() {
    this.update()
  }

  /**
   * 实例化节点信息
   * @description 该方法调用后：nodeMap/attrMap可访问
   * @returns
   */
  private initNodes() {
    const {
      dataHandler: {
        originData: { nodeDataArray: nodes },
      },
      nodeRelatedLineMap,
    } = this
    if (!nodes?.length) return
    for (let i = 0; i < nodes.length; i++) {
      const it = nodes[i]
      let node = this.nodeMap.get(it.id)
      if (!node) {
        node = new Node(this.canvas.cacheCtx, {
          id: it.id,
          enName: it.name,
          cnName: it.cnName,
          type: it.type,
          x: it.x,
          y: it.y,
          width: it.width,
          height: it.height,
          attributes: it.attributes,
          bdC: it.bdC,
          bgC: it.bgC,
          quotePrimaryKey: it.quotePrimaryKey,
          physicalTables: it.physicalTables,
        })
        this.nodeMap.set(it.id, node)
        if (!nodeRelatedLineMap.has(it.id))
          nodeRelatedLineMap.set(it.id, { from: new Set(), to: new Set() })

        // for (let j = 0; j < node.attributes.length; j++) {
        //   const attr = node.attributes[j]
        //   this.attrMap.set(attr.id, node)
        // }
      }
    }
  }

  private renderNodes() {
    const {
      nodeMap,
      dataHandler: { originData },
      renderElements,
    } = this
    const nodes = originData.nodeDataArray
    for (let i = 0; i < nodes.length; i++) {
      const it = nodes[i]
      const node = nodeMap.get(it.id)
      node.updateOptions(it)
      const isVisible = this.canvas.isVisible(node.boundingBox)
      if (isVisible) {
        renderElements.push(node)
        node.draw()
      }
    }
  }

  private renderLines() {
    const {
      lineMap,
      dataHandler: { originData },
      renderElements,
      nodeRelatedLineMap,
    } = this
    const lines = originData.linkDataArray
    for (let i = 0; i < lines.length; i++) {
      const it = lines[i]
      let line = lineMap.get(it.id)

      this._checkLinePos(it)

      if (!line) {
        line = new LineErwin(this.canvas.cacheCtx, {
          pos: it.pos,
          type: it.type,
          from: it.from,
          to: it.to,
          id: it.id,
          numLineTypes: it.numLineTypes,
        })
        this.lineMap.set(it.id, line)
      } else {
        line.updateOptions(it)
      }
      // 缓存节点与连线的关系map
      if (!nodeRelatedLineMap.has(line.from))
        nodeRelatedLineMap.set(line.from, { from: new Set(), to: new Set() })
      if (!nodeRelatedLineMap.has(line.to))
        nodeRelatedLineMap.set(line.to, { from: new Set(), to: new Set() })
      nodeRelatedLineMap.get(line.from).from.add(line)
      nodeRelatedLineMap.get(line.to).to.add(line)

      const isVisible = this.canvas.isVisible(line.boundingBox)
      if (isVisible) {
        renderElements.push(line)
        line.draw()
      }
    }
  }

  private renderNote() {
    const {
      dataHandler: {
        originData: { noteDataArray },
      },
      noteMap,
      renderElements,
    } = this

    for (let i = 0; i < noteDataArray.length; i++) {
      const it = noteDataArray[i]
      let note = noteMap.get(it.id)

      if (!note) {
        note = new TextBase(this.canvas.cacheCtx, {
          id: it.id,
          content: it.text,
          textColor: it.color,
          x: it.x,
          y: it.y,
        })
        this.noteMap.set(it.id, note)
      } else {
        note.updateOptions(it)
      }

      const isVisible = this.canvas.isVisible(note.boundingBox)
      if (isVisible) {
        renderElements.push(note)
        note.draw()
      }
    }
  }

  public updateLayout() {
    const originData = this.dataHandler.originData
    hierarchicalLayout(originData.nodeDataArray, originData.linkDataArray)
    this.update()
  }

  //根据数据更新视图
  update(newData?) {
    this.renderElements = []
    //外部可能会改变data指向，当改变data指向时，调用该方法传入新的data
    //并且清空聚焦节点
    if (newData) {
      this.dataHandler.update({
        data: newData,
      })
      this.lineMap = new Map()
      this.markMap = new Map()
      this.noteMap = new Map()
      this.nodeMap = new Map()
      this.nodeRelatedLineMap = new Map()
      this.focusTarget.clear()

      this.initNodes()
    }

    this.canvas.clearCacheCanvas()
    const cacheCtx = this.cacheCtx
    cacheCtx.font = '15px ' + this.fontFamily

    // 开始缓存canvas绘制
    cacheCtx.save()

    // 绘制矩形框
    this.renderMarks()
    // 绘制连线
    this.renderLines()
    // 绘制实体
    this.renderNodes()
    // 绘制文本备注信息
    this.renderNote()

    cacheCtx.restore()

    this.canvas.update()
    this.updateEventCanvas()
    this.updateThumbnail()
  }

  public async updateThumbnail() {
    const {
      dataHandler: {
        originData: { nodeDataArray },
      },
    } = this

    const thumbnail = this.plugins.get(PluginsEnum.Thumbnail)
    if (!thumbnail) return false

    await nextTick()

    const { width: viewW, height: viewH } = this.canvas.getRenderSize()
    let minX = 0,
      minY = 0,
      maxX = viewW,
      maxY = viewH
    for (let i = 0; i < nodeDataArray.length; i++) {
      const node = nodeDataArray[i]
      const { x, y, width, height } = node

      if (x < minX) minX = x
      if (y < minY) minY = y
      if (x + width > maxX) maxX = x + width
      if (y + height > maxY) maxY = y + height
    }

    thumbnail.draw(minX, minY, maxX, maxY, (ctx) => {
      for (let i = 0; i < nodeDataArray.length; i++) {
        const node = nodeDataArray[i]
        const { x, y, width, height, bgC, bdC } = node
        ctx.save()

        ctx.fillStyle = bgC || '#fff'
        ctx.fillRect(x, y, width, height)
        ctx.strokeStyle = bdC || '#000'
        ctx.strokeRect(x, y, width, height)
        ctx.restore()
      }
    })
  }

  public updateEventCanvas() {
    this.emit(GraphEvent.BeforeUpdate, this.canvas.eventCtx)
    this.canvas.clearEventCanvas()
    this.drawFocusElement()
    this.drawFocusAttribute()
    this.drawHoverElement()
    this.drawLockIcon()
    this.emit(GraphEvent.AfterUpdate, this.canvas.eventCtx)
  }

  private drawFocusElement() {
    const { canvas, focusTarget, eventObj } = this

    focusTarget.forEach((el) => {
      if (el instanceof Node) {
        new FrameResize(canvas.eventCtx, {
          x: el.x,
          y: el.y,
          width: el.width,
          height: el.height,
        }).draw()
      } else if (el instanceof LineErwin) {
        new LineErwin(canvas.eventCtx, {
          ...el,
          lineWidth: 3,
          strokeStyle: Color.primary,
        }).draw()
      } else if (el instanceof NodeRect) {
        new NodeRect(canvas.eventCtx, {
          ...el,
        }).draw()
      }
    })
  }

  private drawFocusAttribute() {
    const {
      canvas,
      focusTarget,
      eventObj: { targetAttrId, target },
    } = this

    if (!targetAttrId) return
    const attrInfo = target.getAttributeInfo(targetAttrId)
    canvas.eventCtx.save()
    canvas.eventCtx.fillStyle = target.attrFocusStyle.backgroundColor
    canvas.eventCtx.fillRect(
      attrInfo.x,
      attrInfo.y,
      attrInfo.width,
      attrInfo.height,
    )
    canvas.eventCtx.restore()
  }

  private drawHoverElement() {
    const { canvas, hoverTarget, focusTarget } = this

    const isFocusTarget = focusTarget.has(hoverTarget)

    if (!hoverTarget || isFocusTarget) return

    if (hoverTarget instanceof Node) {
      new FrameHover(canvas.eventCtx, {
        x: hoverTarget.x,
        y: hoverTarget.y,
        width: hoverTarget.width,
        height: hoverTarget.height,
      }).draw()
    } else if (hoverTarget instanceof LineErwin) {
      new LineErwin(canvas.eventCtx, {
        ...hoverTarget,
        lineWidth: 2,
        strokeStyle: Color.primary,
      }).draw()
    }
  }

  copy() {
    if (!this.focusTarget.size) return
    const copyTarget = JSON.parse(JSON.stringify([...this.focusTarget]))
    const { handleCopy } = this.externalFn
    // eslint-disable-next-line
    // @ts-ignore 触发外部函数，通知要执行拷贝操作，并且记录是否允许拷贝当前项
    const allowCopy = handleCopy?.(copyTarget, this.copyTarget) !== false
    if (!allowCopy) return false

    this.copyTarget = copyTarget
    this.editType = EditType.copy
    this.el.style.cursor = Cursor.copy
  }

  //修改可编辑状态
  setEditable(val) {
    this.editable = val
    this.update()
  }

  // 绘制锁
  private drawLockIcon() {
    if (this.editable === Editable.Edit) return

    const translatePos = this.canvas.getTranslate()
    new LockIcon(this.canvas.eventCtx, {
      x: -translatePos.x + this.canvas.width - 40,
      y: -translatePos.y + 25,
    }).draw()
  }

  /**
   * 验证线坐标是否合法
   *    场景：当种种原因造成线坐标混乱时，使用该方法在绘制前把错误数据恢复很有必要，例如：协同操作时
   * @param lineInfo
   * @returns
   */
  _checkLinePos(lineInfo) {
    const {
      dataHandler: { originDataMap },
    } = this
    // 坐标数量不正确时 -> 重新生成连线
    const nonPos = lineInfo.pos?.length < 2 || lineInfo.pos?.length > 4
    if (nonPos) return this.mergeLines([lineInfo])

    const fromNode = originDataMap.get(lineInfo.from)
    const toNode = originDataMap.get(lineInfo.to)
    const fromNodeBord = this.getPointBelongBord(fromNode, lineInfo.pos[0])
    const toNodeBord = this.getPointBelongBord(
      toNode,
      lineInfo.pos[lineInfo.pos.length - 1],
    )

    // 验证通过
    if (fromNodeBord && toNodeBord) return true

    /**
     * 验证不通过时 -> 重新生成连线
     *    实现细节可优化：例如找到当前坐标矩阵的最新距离等
     */
    this.mergeLines([lineInfo])
  }

  //绘制聚焦标识
  _drawFocus(x, y, w, h) {
    const ctx = this.cacheCtx
    ctx.save()
    ctx.fillStyle = '#fff'
    ctx.strokeStyle = '#B4B4B4'
    ctx.shadowColor = '#DADADA'
    ctx.shadowBlur = 5
    ctx.fillRect(x - 3, y - 3, 6, 6)
    ctx.fillRect(x + w / 2 - 3, y - 3, 6, 6)
    ctx.fillRect(x + w - 3, y - 3, 6, 6)
    ctx.fillRect(x - 3, y + h / 2 - 3, 6, 6)
    ctx.fillRect(x + w - 3, y + h / 2 - 3, 6, 6)
    ctx.fillRect(x - 3, y + h - 3, 6, 6)
    ctx.fillRect(x + w / 2 - 3, y + h - 3, 6, 6)
    ctx.fillRect(x + w - 3, y + h - 3, 6, 6)

    ctx.strokeRect(x - 3, y - 3, 6, 6)
    ctx.strokeRect(x + w / 2 - 3, y - 3, 6, 6)
    ctx.strokeRect(x + w - 3, y - 3, 6, 6)
    ctx.strokeRect(x - 3, y + h / 2 - 3, 6, 6)
    ctx.strokeRect(x + w - 3, y + h / 2 - 3, 6, 6)
    ctx.strokeRect(x - 3, y + h - 3, 6, 6)
    ctx.strokeRect(x + w / 2 - 3, y + h - 3, 6, 6)
    ctx.strokeRect(x + w - 3, y + h - 3, 6, 6)

    ctx.restore()
  }

  //绘制表名
  _drawTitle(name, w, x, y) {
    const ctx = this.cacheCtx

    ctx.save()
    // 设置字体
    ctx.font = '15px ' + this.fontFamily
    // 设置颜色
    ctx.fillStyle = '#000'
    // 设置水平对齐方式
    ctx.textAlign = 'left'
    // 设置垂直对齐方式
    ctx.textBaseline = 'top'
    ctx.fillText(ctx.fittingString(name, w - 14), x, y - 24)
    ctx.restore()
  }

  //绘制检出标志
  _drawIcon(img, w, x, y, bdC) {
    const ctx = this.cacheCtx

    ctx.save()
    ctx.beginPath()
    ctx.strokeStyle = bdC
    ctx.lineWidth = 3
    ctx.arc(x + w - 6, y - 18, 9, 0, 2 * Math.PI)
    ctx.clip()
    ctx.drawImage(img, x + w - 15, y - 27, 18, 18)
    ctx.stroke()
    ctx.restore()
  }

  //绘制矩形框
  renderMarks() {
    // eslint-disable-next-line @typescript-eslint/no-this-alias
    const _this = this,
      data = this.dataHandler.originData
    data?.markDataArray?.forEach((item) => {
      // console.log(_this.focusTarget,"focusTarget=====")
      const w = item.width
      const h = item.height
      const x = Number(item.x)
      const y = Number(item.y)
      // const mark = new NodeRect(this.cacheCtx,item)

      const it = item
      let mark = this.markMap.get(it.id)

      if (!mark) {
        mark = new NodeRect(this.canvas.cacheCtx, {
          id: it.id,
          x: x,
          y: y,
          width: w,
          height: h,
        })
        this.markMap.set(it.id, mark)
      } else {
        mark.updateOptions(it)
      }

      const isVisible = this.canvas.isVisible(mark.boundingBox)
      if (isVisible) {
        this.renderElements.push(mark)
        mark.draw()
      }

      if (_this.focusTarget.has(mark)) _this._drawFocus(x, y, w, h)
    })
  }

  /**
   * 放大&缩小视图
   * 参数：scale - 原始大小：100；2倍大小200...
   */
  public zoom(rate) {
    const fixRate = rate / 100
    this.canvas.zoom(fixRate)
    this.update()
    this.emit(GraphEvent.Zoom, this.canvas.zoomRate)
  }

  //找到连线点在节点的哪条边上
  getPointBelongBord(nodeInfo, pointInfo) {
    if (pointInfo.x === nodeInfo.x) {
      return 'L'
    } else if (pointInfo.x === nodeInfo.x + nodeInfo.width) {
      return 'R'
    } else if (pointInfo.y === nodeInfo.y) {
      return 'T'
    } else if (pointInfo.y === nodeInfo.y + nodeInfo.height) {
      return 'B'
    } else {
      console.error(
        'getPointBelongBord未找到匹配边，detail:',
        nodeInfo,
        JSON.parse(JSON.stringify(nodeInfo)),
        JSON.parse(JSON.stringify(pointInfo)),
      )
    }
  }

  //修改节点尺寸
  resizeNode(evX, evY, eventObj, minWidth = 50) {
    //分支不允许修改尺寸
    if (['exclusive', 'inclusive'].includes(eventObj.target.type)) return

    //开始修改尺寸逻辑
    // eslint-disable-next-line @typescript-eslint/no-this-alias
    const _this = this
    const copyEventObjTarget = {
      x: eventObj.target.x,
      y: eventObj.target.y,
      width: eventObj.target.width,
      height: eventObj.target.height,
    }

    //左上
    if (eventObj.targetDirection === 'LT') {
      const newWidth = eventObj.target.width + eventObj.target.x - evX
      const newHeight = eventObj.target.height + eventObj.target.y - evY

      eventObj.target.updateOptions({
        x: newWidth < minWidth ? eventObj.target.x : evX,
        y: newHeight < minWidth ? eventObj.target.y : evY,
        width: newWidth < minWidth ? eventObj.target.width : newWidth,
        height: newHeight < minWidth ? eventObj.target.height : newHeight,
      })
      moveKeyPoint()
    }
    //上中
    else if (eventObj.targetDirection === 'TM') {
      const newHeight = eventObj.target.height + eventObj.target.y - evY

      eventObj.target.updateOptions({
        y: newHeight < minWidth ? eventObj.target.y : evY,
        height: newHeight < minWidth ? eventObj.target.height : newHeight,
      })
      moveKeyPoint()
    }
    //右上
    else if (eventObj.targetDirection === 'RT') {
      const newWidth = evX - eventObj.target.x
      const newHeight = eventObj.target.height + eventObj.target.y - evY

      eventObj.target.updateOptions({
        y: newHeight < minWidth ? eventObj.target.y : evY,
        width: newWidth < minWidth ? eventObj.target.width : newWidth,
        height: newHeight < minWidth ? eventObj.target.height : newHeight,
      })
      moveKeyPoint()
    }
    //左中
    else if (eventObj.targetDirection === 'LM') {
      const newWidth = eventObj.target.width + eventObj.target.x - evX

      eventObj.target.updateOptions({
        x: newWidth < minWidth ? eventObj.target.x : evX,
        width: newWidth < minWidth ? eventObj.target.width : newWidth,
      })
      moveKeyPoint()
    }
    //右中
    else if (eventObj.targetDirection === 'RM') {
      const newWidth = evX - eventObj.target.x

      eventObj.target.updateOptions({
        width: newWidth < minWidth ? eventObj.target.width : newWidth,
      })
      moveKeyPoint()
    }
    //左下
    else if (eventObj.targetDirection === 'LB') {
      const newWidth = eventObj.target.width + eventObj.target.x - evX
      const newHeight = evY - eventObj.target.y

      eventObj.target.updateOptions({
        x: newWidth < minWidth ? eventObj.target.x : evX,
        width: newWidth < minWidth ? eventObj.target.width : newWidth,
        height: newHeight < minWidth ? eventObj.target.height : newHeight,
      })
      moveKeyPoint()
    }
    //下中
    else if (eventObj.targetDirection === 'BM') {
      const newHeight = evY - eventObj.target.y

      eventObj.target.updateOptions({
        height: newHeight < minWidth ? eventObj.target.height : newHeight,
      })
      moveKeyPoint()
    }
    //右下
    else if (eventObj.targetDirection === 'RB') {
      const newWidth = evX - eventObj.target.x
      const newHeight = evY - eventObj.target.y

      eventObj.target.updateOptions({
        width: newWidth < minWidth ? eventObj.target.width : newWidth,
        height: newHeight < minWidth ? eventObj.target.height : newHeight,
      })

      moveKeyPoint()
    }
    if (eventObj.target.type === 'mark') {
      this.emit(MarkEvent.Resize, eventObj.target)
    } else {
      this.emit(NodeEvent.Resize, eventObj.target)
    }

    //根据节点最新位置更新该节点上的连线关键点函数
    function moveKeyPoint() {
      //如果方块没有最小宽度限度证明是备注框，无需判断连线
      if (!minWidth) return
      _this.nodeMap.get(eventObj.target.id).updateOptions(eventObj.target)

      const lineSet = _this.nodeRelatedLineMap.get(
        eventObj.target.id,
      ) as RelatedLineItem

      ;[...lineSet.from, ...lineSet.to].forEach((item) => {
        //判断该条线的起始节点和终结节点是不同的实体：如果不同走实体影响线的函数；如果相同代表是自己连自己的情况，走单独的函数
        const diffFlag = item.from !== item.to
        const idx = lineSet.from.has(item) ? 0 : item.pos.length - 1
        diffFlag && updateKeyPoint(item, idx)
        diffFlag
          ? _this.moveLineForNode(item, eventObj, eventObj.target)
          : _this.moveLineForSameNode(item, eventObj)
      })

      //更新关键点函数
      function updateKeyPoint(item, idx) {
        //左边
        if (item.pos[idx].x === copyEventObjTarget.x) {
          item.pos[idx].x = eventObj.target.x
          item.pos[idx].y =
            item.pos[idx].y < eventObj.target.y
              ? eventObj.target.y + 1
              : item.pos[idx].y > eventObj.target.y + eventObj.target.height
              ? eventObj.target.y + eventObj.target.height - 1
              : item.pos[idx].y
        }
        //右边
        else if (
          item.pos[idx].x ===
          copyEventObjTarget.x + copyEventObjTarget.width
        ) {
          item.pos[idx].x = eventObj.target.x + eventObj.target.width
          item.pos[idx].y =
            item.pos[idx].y < eventObj.target.y
              ? eventObj.target.y + 1
              : item.pos[idx].y > eventObj.target.y + eventObj.target.height
              ? eventObj.target.y + eventObj.target.height - 1
              : item.pos[idx].y
        }
        //上边
        else if (item.pos[idx].y === copyEventObjTarget.y) {
          item.pos[idx].x =
            item.pos[idx].x <= eventObj.target.x
              ? eventObj.target.x + 1
              : item.pos[idx].x >= eventObj.target.x + eventObj.target.width
              ? eventObj.target.x + eventObj.target.width - 1
              : item.pos[idx].x
          item.pos[idx].y = eventObj.target.y
        }
        //下边
        else if (
          item.pos[idx].y ===
          copyEventObjTarget.y + copyEventObjTarget.height
        ) {
          item.pos[idx].x =
            item.pos[idx].x <= eventObj.target.x
              ? eventObj.target.x + 1
              : item.pos[idx].x >= eventObj.target.x + eventObj.target.width
              ? eventObj.target.x + eventObj.target.width - 1
              : item.pos[idx].x
          item.pos[idx].y = eventObj.target.y + eventObj.target.height
        }
      }
    }
  }

  //根据四个边获取坐标点
  getNodePosFromBoreder(targetNode, direction) {
    let pos = {
      start: {
        x: null,
        y: null,
      },
      end: {
        x: null,
        y: null,
      },
    }
    switch (direction) {
      case 'T':
        pos = {
          start: {
            x: targetNode.x,
            y: targetNode.y,
          },
          end: {
            x: targetNode.x + targetNode.width,
            y: targetNode.y,
          },
        }
        break
      case 'B':
        pos = {
          start: {
            x: targetNode.x,
            y: targetNode.y + targetNode.height,
          },
          end: {
            x: targetNode.x + targetNode.width,
            y: targetNode.y + targetNode.height,
          },
        }
        break
      case 'L':
        pos = {
          start: {
            x: targetNode.x,
            y: targetNode.y,
          },
          end: {
            x: targetNode.x,
            y: targetNode.y + targetNode.height,
          },
        }
        break
      case 'R':
        pos = {
          start: {
            x: targetNode.x + targetNode.width,
            y: targetNode.y,
          },
          end: {
            x: targetNode.x + targetNode.width,
            y: targetNode.y + targetNode.height,
          },
        }
        break
    }
    return pos
  }

  //更新 节点移动 触发的更改线条路径
  moveLineForNode(linkInfo, eventObj, nodeNewLoc) {
    const posMaxIdx = linkInfo.pos.length - 1
    const distance = {
      x: nodeNewLoc.x - eventObj.target.x,
      y: nodeNewLoc.y - eventObj.target.y,
    }

    const fromNode = this.nodeMap.get(linkInfo.from)
    const toNode = this.nodeMap.get(linkInfo.to)
    const fromNodeBord = this.getPointBelongBord(fromNode, linkInfo.pos[0])
    const toNodeBord = this.getPointBelongBord(toNode, linkInfo.pos[posMaxIdx])

    // 连线错乱时 -> 重新生成连线坐标
    if (!fromNodeBord || !toNodeBord) this.mergeLines([linkInfo])

    const moveTargetIsFrom = eventObj.target.id === fromNode.id

    //拖动节点是from节点
    if (moveTargetIsFrom) {
      //更新首点定位
      linkInfo.pos[0].x = nodeNewLoc.x + (linkInfo.pos[0].x - eventObj.target.x)
      linkInfo.pos[0].y = nodeNewLoc.y + (linkInfo.pos[0].y - eventObj.target.y)
    }
    //拖动节点是to节点
    else {
      //更新末点定位
      linkInfo.pos[posMaxIdx].x =
        nodeNewLoc.x + (linkInfo.pos[posMaxIdx].x - eventObj.target.x)
      linkInfo.pos[posMaxIdx].y =
        nodeNewLoc.y + (linkInfo.pos[posMaxIdx].y - eventObj.target.y)
    }

    //根据最新的定位点更新连线点坐标
    //直线情况
    if (posMaxIdx === 1) {
      linkInfo.pos.splice(
        1,
        0,
        { x: linkInfo.pos[0].x, y: linkInfo.pos[0].y },
        { x: linkInfo.pos[0].x, y: linkInfo.pos[0].y },
      )
    }
    //二折情况
    else if (posMaxIdx === 2) {
      if (fromNodeBord === 'R' && toNodeBord === 'B') {
        //正常向下
        if (
          this.getNodePosFromBoreder(toNode, 'B').start.y < linkInfo.pos[0].y
        ) {
          linkInfo.pos[1] = { x: linkInfo.pos[2].x, y: linkInfo.pos[0].y }
        }
        //变横向一折
        else {
          const idx1X =
            this.getNodePosFromBoreder(fromNode, 'R').start.x <
            this.getNodePosFromBoreder(toNode, 'L').start.x
              ? this.getNodePosFromBoreder(toNode, 'L').start.x
              : this.getNodePosFromBoreder(toNode, 'R').start.x
          linkInfo.pos[1] = {
            x: idx1X + (moveTargetIsFrom ? 0 : distance.x),
            y: linkInfo.pos[0].y + distance.y,
          }
          linkInfo.pos.splice(2)
        }

        //向右变竖向一折
        if (linkInfo.pos[2] && linkInfo.pos[2].x <= linkInfo.pos[0].x) {
          linkInfo.pos[0] = {
            x: linkInfo.pos[0].x - 1,
            y: fromNode.y + (moveTargetIsFrom ? distance.y : 0),
          }
          linkInfo.pos[1] = { x: linkInfo.pos[2].x, y: linkInfo.pos[2].y }
          linkInfo.pos.splice(2)
        }
      } else if (fromNodeBord === 'B' && toNodeBord === 'R') {
        if (fromNode.y + fromNode.height < linkInfo.pos[1].y) {
          linkInfo.pos[1] = { x: linkInfo.pos[0].x, y: linkInfo.pos[2].y }
        }
        //变一折
        else {
          if (['exclusive', 'inclusive'].includes(fromNode.type)) {
            linkInfo.pos[1] = {
              x: linkInfo.pos[0].x,
              y:
                Math.max(
                  this.getNodePosFromBoreder(fromNode, 'B').start.y,
                  this.getNodePosFromBoreder(toNode, 'B').start.y,
                ) + 20,
            }
            linkInfo.pos[2] = {
              x:
                toNode.x +
                Math.floor(toNode.width / 2) +
                (moveTargetIsFrom ? 0 : distance.x),
              y: linkInfo.pos[1].y,
            }
            linkInfo.pos[3] = {
              x: linkInfo.pos[2].x,
              y:
                this.getNodePosFromBoreder(toNode, 'B').start.y +
                (moveTargetIsFrom ? 0 : distance.y),
            }
          } else {
            linkInfo.pos[0] = {
              x:
                fromNode.x +
                (eventObj.target.id === fromNode.id ? distance.x : 0),
              y: linkInfo.pos[1].y,
            }
            linkInfo.pos[1] = { x: linkInfo.pos[2].x, y: linkInfo.pos[2].y }
            linkInfo.pos.splice(2)
          }
        }

        if (linkInfo.pos[1].x < toNode.x + toNode.width) {
          linkInfo.pos[1].y =
            toNode.y + (eventObj.target.id === toNode.id ? distance.y : 0)
          linkInfo.pos.splice(2)
        }
      } else if (fromNodeBord === 'L' && toNodeBord === 'B') {
        //正常向下
        if (
          this.getNodePosFromBoreder(toNode, 'B').start.y < linkInfo.pos[0].y
        ) {
          linkInfo.pos[1] = { x: linkInfo.pos[2].x, y: linkInfo.pos[0].y }
        }
        //变横向一折
        else {
          linkInfo.pos[1] = {
            x:
              toNode.x +
              toNode.width +
              (eventObj.target.id === toNode.id ? distance.x : 0),
            y: linkInfo.pos[0].y,
          }
          linkInfo.pos.splice(2)
        }
        //变竖向一折
        if (
          linkInfo.pos[1].x >= eventObj.target.x &&
          this.getNodePosFromBoreder(toNode, 'B').start.y <
            this.getNodePosFromBoreder(fromNode, 'T').start.y
        ) {
          linkInfo.pos[0] = {
            x: linkInfo.pos[0].x + (moveTargetIsFrom ? distance.x : 0) - 1,
            y: fromNode.y + (moveTargetIsFrom ? distance.y : 0),
          }
          linkInfo.pos[1] = { x: linkInfo.pos[2].x, y: linkInfo.pos[2].y }
          linkInfo.pos.splice(2)
        }
      } else if (fromNodeBord === 'B' && toNodeBord === 'L') {
        linkInfo.pos[1].x = linkInfo.pos[0].x
        linkInfo.pos[1].y = linkInfo.pos[2].y
        if (linkInfo.pos[1].x > toNode.x) {
          linkInfo.pos[1].y =
            toNode.y + (eventObj.target.id === toNode.id ? distance.y : 0)
          linkInfo.pos.splice(2)
        }
        if (linkInfo.pos[1].y < linkInfo.pos[0].y) {
          if (['exclusive', 'inclusive'].includes(fromNode.type)) {
            linkInfo.pos[1] = {
              x: linkInfo.pos[0].x,
              y:
                Math.max(
                  this.getNodePosFromBoreder(fromNode, 'B').start.y,
                  this.getNodePosFromBoreder(toNode, 'B').start.y,
                ) + 20,
            }
            linkInfo.pos[2] = {
              x:
                toNode.x +
                Math.floor(toNode.width / 2) +
                (moveTargetIsFrom ? 0 : distance.x),
              y: linkInfo.pos[1].y,
            }
            linkInfo.pos[3] = {
              x: linkInfo.pos[2].x,
              y:
                this.getNodePosFromBoreder(toNode, 'B').start.y +
                (moveTargetIsFrom ? 0 : distance.y),
            }
          } else {
            linkInfo.pos[1].x =
              this.getNodePosFromBoreder(fromNode, 'R').start.x +
              (eventObj.target.id === fromNode.id ? distance.x : 0)
            linkInfo.pos[1].y =
              this.getNodePosFromBoreder(fromNode, 'B').start.y +
              (eventObj.target.id === fromNode.id ? distance.y : 0)
            linkInfo.pos.splice(0, 1)
          }
        }
      } else if (fromNodeBord === 'R' && toNodeBord === 'T') {
        linkInfo.pos[1].x = linkInfo.pos[2].x
        linkInfo.pos[1].y = linkInfo.pos[0].y
        if (linkInfo.pos[1].x < linkInfo.pos[0].x) {
          linkInfo.pos[1].y =
            this.getNodePosFromBoreder(fromNode, 'B').start.y +
            (eventObj.target.id === fromNode.id ? distance.y : 0)
          linkInfo.pos.splice(0, 1)
        }
        if (linkInfo.pos[2] && linkInfo.pos[1].y > linkInfo.pos[2].y) {
          if (['exclusive', 'inclusive'].includes(toNode.type)) {
            linkInfo.pos[0] = {
              x: fromNode.x + Math.floor(fromNode.width / 2),
              y: fromNode.y + (moveTargetIsFrom ? distance.y : 0),
            }
            linkInfo.pos[1] = {
              x: linkInfo.pos[0].x,
              y: linkInfo.pos[0].y - 20,
            }
            linkInfo.pos[2] = { x: linkInfo.pos[2].x, y: linkInfo.pos[1].y }
            linkInfo.pos[3] = {
              x: linkInfo.pos[2].x,
              y: toNode.y + (moveTargetIsFrom ? 0 : distance.y),
            }
          } else {
            linkInfo.pos[1].x = toNode.x + (moveTargetIsFrom ? 0 : distance.x)
            linkInfo.pos[1].y = toNode.y + (moveTargetIsFrom ? 0 : distance.y)
            linkInfo.pos.splice(2)
          }
        }
      } else if (fromNodeBord === 'T' && toNodeBord === 'R') {
        linkInfo.pos[1].x = linkInfo.pos[0].x
        linkInfo.pos[1].y = linkInfo.pos[2].y
        if (linkInfo.pos[1].x < linkInfo.pos[2].x) {
          linkInfo.pos[1].y =
            this.getNodePosFromBoreder(toNode, 'B').start.y +
            (eventObj.target.id === toNode.id ? distance.y : 0)
          linkInfo.pos.splice(2)
        }
        if (linkInfo.pos[0].y < linkInfo.pos[1].y) {
          linkInfo.pos[1].x =
            fromNode.x + (eventObj.target.id === fromNode.id ? distance.x : 0)
          linkInfo.pos[1].y =
            fromNode.y + (eventObj.target.id === fromNode.id ? distance.y : 0)
          linkInfo.pos.splice(0, 1)
        }
      } else if (fromNodeBord === 'T' && toNodeBord === 'L') {
        linkInfo.pos[1].x = linkInfo.pos[0].x
        linkInfo.pos[1].y = linkInfo.pos[2].y
        if (linkInfo.pos[1].x > linkInfo.pos[2].x) {
          linkInfo.pos[1].y =
            this.getNodePosFromBoreder(toNode, 'B').start.y +
            (eventObj.target.id === toNode.id ? distance.y : 0)
          linkInfo.pos.splice(2)
        }
        if (linkInfo.pos[1].y > linkInfo.pos[0].y) {
          linkInfo.pos[1].x =
            this.getNodePosFromBoreder(fromNode, 'R').start.x +
            (eventObj.target.id === fromNode.id ? distance.x : 0)
          linkInfo.pos.splice(0, 1)
        }
      } else if (fromNodeBord === 'L' && toNodeBord === 'T') {
        linkInfo.pos[1].x = linkInfo.pos[2].x
        linkInfo.pos[1].y = linkInfo.pos[0].y
        if (linkInfo.pos[1].y > linkInfo.pos[2].y) {
          if (['exclusive', 'inclusive'].includes(toNode.type)) {
            linkInfo.pos[0] = {
              x: fromNode.x + Math.floor(fromNode.width / 2),
              y: fromNode.y + (moveTargetIsFrom ? distance.y : 0),
            }
            linkInfo.pos[1] = {
              x: linkInfo.pos[0].x,
              y: linkInfo.pos[0].y - 20,
            }
            linkInfo.pos[2] = { x: linkInfo.pos[2].x, y: linkInfo.pos[1].y }
            linkInfo.pos[3] = {
              x: linkInfo.pos[2].x,
              y: toNode.y + (moveTargetIsFrom ? 0 : distance.y),
            }
          } else {
            linkInfo.pos[1].x =
              this.getNodePosFromBoreder(toNode, 'R').start.x +
              (eventObj.target.id === toNode.id ? distance.x : 0)
            linkInfo.pos.splice(2)
          }
        }
        if (linkInfo.pos[1].x > linkInfo.pos[0].x) {
          linkInfo.pos[1].y =
            this.getNodePosFromBoreder(fromNode, 'B').start.y +
            (eventObj.target.id === fromNode.id ? distance.y : 0)
          linkInfo.pos.splice(0, 1)
        }
      }
    }
    //三折情况
    else if (posMaxIdx === 3) {
      const middleX = Math.floor(
        Math.abs(linkInfo.pos[0].x - linkInfo.pos[posMaxIdx].x) / 2,
      )
      const middleY = Math.floor(
        Math.abs(linkInfo.pos[0].y - linkInfo.pos[posMaxIdx].y) / 2,
      )

      if (fromNodeBord === 'B' && toNodeBord === 'B') {
        // 正常向下移动
        if (
          this.getNodePosFromBoreder(fromNode, 'T').start.y <
          this.getNodePosFromBoreder(toNode, 'B').start.y
        ) {
          //正常向下
          if (
            this.getNodePosFromBoreder(fromNode, 'B').start.y <
            linkInfo.pos[1].y
          ) {
            const idx1and2Y =
              linkInfo.pos[2].y - toNode.y - toNode.height + linkInfo.pos[3].y
            // linkInfo.pos[0] = { x: linkInfo.pos[0].x, y: fromNode.y }
            linkInfo.pos[1] = { x: linkInfo.pos[0].x, y: idx1and2Y }
            linkInfo.pos[2] = { x: linkInfo.pos[3].x, y: idx1and2Y }
            // linkInfo.pos[3] = {x: linkInfo.pos[3].x, y: linkInfo.pos[3].y}
          }
          //二折
          else {
            const idx0X =
              fromNode.x + fromNode.width > linkInfo.pos[3].x
                ? fromNode.x
                : fromNode.x + fromNode.width
            const idx1and2Y =
              linkInfo.pos[2].y - toNode.y - toNode.height + linkInfo.pos[3].y
            linkInfo.pos[0] = {
              x: idx0X + (eventObj.target.id === fromNode.id ? distance.x : 0),
              y: idx1and2Y,
            }
            linkInfo.pos[1] = { x: linkInfo.pos[3].x, y: idx1and2Y }
            linkInfo.pos[2] = { x: linkInfo.pos[3].x, y: linkInfo.pos[3].y }
            // 删除最后一位
            linkInfo.pos.splice(3)
          }
        }
        // from和to重合点 -> 变成T to B
        else {
          if (['exclusive', 'inclusive'].includes(fromNode.type)) {
            linkInfo.pos[1] = {
              x: linkInfo.pos[0].x,
              y: fromNode.y + fromNode.height + 20,
            }
            linkInfo.pos[2] = { x: linkInfo.pos[3].x, y: linkInfo.pos[1].y }
          } else {
            // console.log('T->B')
            linkInfo.pos[0] = {
              x: linkInfo.pos[0].x,
              y: linkInfo.pos[0].y - fromNode.height,
            }
            linkInfo.pos[1] = {
              x: linkInfo.pos[0].x,
              y:
                linkInfo.pos[0].y +
                Math.floor((fromNode.y - toNode.y - toNode.height) / 2),
            }
            linkInfo.pos[2] = {
              x: linkInfo.pos[3].x,
              y:
                linkInfo.pos[0].y +
                Math.floor((fromNode.y - toNode.y - toNode.height) / 2),
            }
          }
        }
      } else if (fromNodeBord === 'B' && toNodeBord === 'T') {
        // from高 to低
        if (fromNode.y + fromNode.height < toNode.y) {
          linkInfo.pos[1] = {
            x: linkInfo.pos[0].x,
            y: linkInfo.pos[0].y + middleY,
          }
          linkInfo.pos[2] = {
            x: linkInfo.pos[3].x,
            y: linkInfo.pos[0].y + middleY,
          }
        }
        // from和to重合点 -> 变成B to B
        else if (fromNode.y + fromNode.height >= toNode.y) {
          if (['exclusive', 'inclusive'].includes(toNode.type)) {
            linkInfo.pos[0] = {
              x: linkInfo.pos[0].x,
              y: linkInfo.pos[0].y - fromNode.height,
            }
            linkInfo.pos[1] = {
              x: linkInfo.pos[0].x,
              y: linkInfo.pos[0].y - 20,
            }
            linkInfo.pos[2] = { x: linkInfo.pos[3].x, y: linkInfo.pos[1].y }
          } else {
            // console.log('B->B')
            linkInfo.pos[1] = {
              x: linkInfo.pos[0].x,
              y: toNode.y + toNode.height + 20,
            }
            linkInfo.pos[2] = {
              x: linkInfo.pos[3].x,
              y: toNode.y + toNode.height + 20,
            }
            linkInfo.pos[3] = {
              x: linkInfo.pos[3].x,
              y: linkInfo.pos[3].y + toNode.height,
            }
          }
        }
      } else if (fromNodeBord === 'T' && toNodeBord === 'T') {
        if (
          ['exclusive', 'inclusive'].includes(fromNode.type) ||
          ['exclusive', 'inclusive'].includes(toNode.type)
        ) {
          const y =
            linkInfo.pos[1].y + distance.y > Math.min(fromNode.y, toNode.y) - 20
              ? Math.min(fromNode.y, toNode.y) - 20
              : linkInfo.pos[1].y + distance.y
          linkInfo.pos[1] = { x: linkInfo.pos[0].x, y: y }
          linkInfo.pos[2] = { x: linkInfo.pos[3].x, y: y }
        } else if (linkInfo.pos[1].y < eventObj.target.y) {
          linkInfo.pos[1] = { x: linkInfo.pos[0].x, y: linkInfo.pos[1].y }
          linkInfo.pos[2] = { x: linkInfo.pos[3].x, y: linkInfo.pos[2].y }
        } else {
          // console.log('B->B')
          linkInfo.pos[0] = {
            x: linkInfo.pos[0].x,
            y: linkInfo.pos[0].y + fromNode.height,
          }
          linkInfo.pos[1] = {
            x: linkInfo.pos[0].x,
            y:
              Math.max(fromNode.y + fromNode.height, toNode.y + toNode.height) +
              20,
          }
          linkInfo.pos[2] = {
            x: linkInfo.pos[3].x,
            y:
              Math.max(fromNode.y + fromNode.height, toNode.y + toNode.height) +
              20,
          }
          linkInfo.pos[3] = {
            x: linkInfo.pos[3].x,
            y: linkInfo.pos[3].y + toNode.height,
          }
        }
      } else if (fromNodeBord === 'T' && toNodeBord === 'B') {
        if (fromNode.y > toNode.y + toNode.height) {
          linkInfo.pos[1] = {
            x: linkInfo.pos[0].x,
            y: linkInfo.pos[0].y - middleY,
          }
          linkInfo.pos[2] = {
            x: linkInfo.pos[3].x,
            y: linkInfo.pos[3].y + middleY,
          }
        }
        // from和to重合点 -> 变成B to B
        else if (fromNode.y <= toNode.y + toNode.height) {
          console.log('B->B')
          linkInfo.pos[0] = {
            x: linkInfo.pos[0].x,
            y: linkInfo.pos[0].y + fromNode.height,
          }
          linkInfo.pos[1] = { x: linkInfo.pos[0].x, y: linkInfo.pos[0].y + 20 }
          linkInfo.pos[2] = { x: linkInfo.pos[3].x, y: linkInfo.pos[0].y + 20 }
        }
      } else if (fromNodeBord === 'L' && toNodeBord === 'L') {
        //拖动的是to节点
        if (eventObj.target.id === toNode.id) {
          linkInfo.pos[2] = {
            x: nodeNewLoc.x - (toNode.x - linkInfo.pos[2].x),
            y: linkInfo.pos[3].y,
          }
          linkInfo.pos[1] = { x: linkInfo.pos[2].x, y: linkInfo.pos[0].y }
        }
        //拖动的是from节点
        else {
          linkInfo.pos[1] = { x: linkInfo.pos[2].x, y: linkInfo.pos[0].y }
        }
        //变成二折
        if (
          linkInfo.pos[1].x > fromNode.x &&
          linkInfo.pos[1].x < fromNode.x + fromNode.width
        ) {
          // console.log('变成二折')
          linkInfo.pos.splice(0, 1)
          ++linkInfo.pos[0].x
          linkInfo.pos[0].y =
            (linkInfo.pos[1].y <
            this.getNodePosFromBoreder(fromNode, 'T').start.y
              ? this.getNodePosFromBoreder(fromNode, 'T').start.y
              : this.getNodePosFromBoreder(fromNode, 'B').start.y) +
            (eventObj.target.id === fromNode.id ? distance.y : 0)
        }
      } else if (fromNodeBord === 'L' && toNodeBord === 'R') {
        if (fromNode.x > toNode.x + toNode.width) {
          linkInfo.pos[1] = {
            x: linkInfo.pos[0].x - middleX,
            y: linkInfo.pos[0].y,
          }
          linkInfo.pos[2] = { x: linkInfo.pos[1].x, y: linkInfo.pos[3].y }
        }
        // from和to重合 -> 变成R to R
        else {
          linkInfo.pos[0] = {
            x: linkInfo.pos[0].x + fromNode.width,
            y: linkInfo.pos[0].y,
          }
          linkInfo.pos[1] = { x: linkInfo.pos[0].x + 20, y: linkInfo.pos[0].y }
          linkInfo.pos[2] = { x: linkInfo.pos[0].x + 20, y: linkInfo.pos[3].y }
        }
      } else if (fromNodeBord === 'R' && toNodeBord === 'L') {
        //from在to左边
        if (fromNode.x + fromNode.width < toNode.x) {
          linkInfo.pos[1] = {
            x: linkInfo.pos[0].x + middleX,
            y: linkInfo.pos[0].y,
          }
          linkInfo.pos[2] = {
            x: linkInfo.pos[0].x + middleX,
            y: linkInfo.pos[3].y,
          }
        } else {
          linkInfo.pos[1] = {
            x: linkInfo.pos[0].x + toNode.width + 20,
            y: linkInfo.pos[0].y,
          }
          linkInfo.pos[2] = {
            x: linkInfo.pos[0].x + toNode.width + 20,
            y: linkInfo.pos[3].y,
          }
          linkInfo.pos[3] = {
            x: linkInfo.pos[3].x + toNode.width,
            y: linkInfo.pos[3].y,
          }
        }
      } else if (fromNodeBord === 'R' && toNodeBord === 'R') {
        //拖动的是to节点
        if (eventObj.target.id === toNode.id) {
          linkInfo.pos[2] = {
            x: nodeNewLoc.x - (toNode.x - linkInfo.pos[2].x),
            y: linkInfo.pos[3].y,
          }
          linkInfo.pos[1] = { x: linkInfo.pos[2].x, y: linkInfo.pos[0].y }
        }
        //拖动的是from节点
        else {
          linkInfo.pos[1] = { x: linkInfo.pos[2].x, y: linkInfo.pos[0].y }
        }
        //变成二折
        if (
          linkInfo.pos[1].x > fromNode.x &&
          linkInfo.pos[1].x < fromNode.x + fromNode.width
        ) {
          // console.log('变成二折')
          linkInfo.pos.splice(0, 1)
          --linkInfo.pos[0].x
          linkInfo.pos[0].y =
            (linkInfo.pos[1].y <
            this.getNodePosFromBoreder(fromNode, 'T').start.y
              ? this.getNodePosFromBoreder(fromNode, 'T').start.y
              : this.getNodePosFromBoreder(fromNode, 'B').start.y) +
            (eventObj.target.id === fromNode.id ? distance.y : 0)
        }
      }
    }

    this.emit(LineEvent.Move, linkInfo)
  }

  //更新 节点移动 触发的更改线条路径(自己连自己的情况)
  moveLineForSameNode(linkInfo, eventObj) {
    // const disY = copyEventObjTarget.y+copyEventObjTarget.height-linkInfo.pos[0].y
    // const disX = copyEventObjTarget.x+copyEventObjTarget.width-linkInfo.pos[4].x
    const disY = 20
    const disX = 20
    // console.log(disX, disY)
    const dis0to1 = linkInfo.pos[1].x - linkInfo.pos[0].x
    const dis1to2 = linkInfo.pos[2].y - linkInfo.pos[1].y

    linkInfo.pos[0] = {
      x: eventObj.target.x + eventObj.target.width,
      y: eventObj.target.y + eventObj.target.height - disY,
    }
    linkInfo.pos[4] = {
      x: eventObj.target.x + eventObj.target.width - disX,
      y: eventObj.target.y + eventObj.target.height,
    }
    linkInfo.pos[1] = { x: linkInfo.pos[0].x + dis0to1, y: linkInfo.pos[0].y }
    linkInfo.pos[2] = { x: linkInfo.pos[1].x, y: linkInfo.pos[1].y + dis1to2 }
    linkInfo.pos[3] = { x: linkInfo.pos[4].x, y: linkInfo.pos[2].y }

    this.emit(LineEvent.Move, linkInfo)
  }

  //更新 路径移动 触发的更改线条路径
  moveLineForLine(eventObj, mouseLoc) {
    const eventPos = eventObj.target.pos,
      posMaxIdx = eventPos.length - 1
    let fromNode,
      toNode,
      total = 0

    this.dataHandler.originData.nodeDataArray?.forEach((item) => {
      if (item.id === eventObj.target.from) {
        fromNode = item
        total++
      }
      if (item.id === eventObj.target.to) {
        toNode = item
        total++
      }
      if (total === 2) return
    })

    //分支节点位置限制: from
    if (['exclusive', 'inclusive'].includes(fromNode.type)) {
      //挨着from的竖线
      if (
        eventObj.targetLineStartPointIdx === 0 &&
        eventPos[eventObj.targetLineStartPointIdx].x ===
          eventPos[eventObj.targetLineStartPointIdx + 1].x &&
        (this.getNodePosFromBoreder(fromNode, 'L').start.x >= mouseLoc.x ||
          this.getNodePosFromBoreder(fromNode, 'R').start.x <= mouseLoc.x)
      )
        return
      //中间横线
      if (
        eventObj.targetLineStartPointIdx === 1 &&
        eventPos[eventObj.targetLineStartPointIdx].y ===
          eventPos[eventObj.targetLineStartPointIdx + 1].y &&
        this.getNodePosFromBoreder(fromNode, 'B').start.y >= mouseLoc.y
      )
        return
      //挨着to的竖线
      if (
        eventObj.targetLineStartPointIdx === 2 &&
        eventPos[eventObj.targetLineStartPointIdx].x ===
          eventPos[eventObj.targetLineStartPointIdx + 1].x &&
        (this.getNodePosFromBoreder(toNode, 'L').start.x >= mouseLoc.x ||
          this.getNodePosFromBoreder(toNode, 'R').start.x <= mouseLoc.x)
      )
        return
    }
    //分支节点位置限制: to
    else if (['exclusive', 'inclusive'].includes(toNode.type)) {
      //一折线不能移动
      if (posMaxIdx === 1) return
      //二折线竖线不能动
      if (posMaxIdx === 2 && eventObj.targetLineStartPointIdx === 1) return
      //二折线横线限制
      if (
        posMaxIdx === 2 &&
        eventObj.targetLineStartPointIdx === 0 &&
        this.getNodePosFromBoreder(toNode, 'T').start.y < mouseLoc.y
      )
        return
      //三折最后的线不能动
      if (posMaxIdx === 3 && eventObj.targetLineStartPointIdx === 2) return
      //三折中间横线位置限制
      if (
        posMaxIdx === 3 &&
        eventObj.targetLineStartPointIdx === 1 &&
        this.getNodePosFromBoreder(toNode, 'T').start.y < mouseLoc.y
      )
        return
      //三折第一条线位置限制
      if (
        posMaxIdx === 3 &&
        eventObj.targetLineStartPointIdx === 0 &&
        (this.getNodePosFromBoreder(fromNode, 'L').start.x >= mouseLoc.x ||
          this.getNodePosFromBoreder(fromNode, 'R').start.x <= mouseLoc.x)
      )
        return
    }

    //一折情况
    if (posMaxIdx === 1) {
      //横线
      if (eventPos[0].y === eventPos[1].y) {
        //更新y坐标
        eventPos[0].y = mouseLoc.y
        eventPos[1].y = mouseLoc.y
        //更新x坐标
        if (
          this.getNodePosFromBoreder(fromNode, 'T').start.y > mouseLoc.y ||
          this.getNodePosFromBoreder(fromNode, 'B').start.y < mouseLoc.y
        ) {
          eventPos[0].x = fromNode.x + fromNode.width / 2
          eventPos.unshift({
            x: fromNode.x + fromNode.width / 2,
            y:
              this.getNodePosFromBoreder(fromNode, 'T').start.y > mouseLoc.y
                ? fromNode.y
                : fromNode.y + fromNode.height,
          })
        }
        if (
          this.getNodePosFromBoreder(toNode, 'T').start.y > mouseLoc.y ||
          this.getNodePosFromBoreder(toNode, 'B').start.y < mouseLoc.y
        ) {
          eventPos[eventPos.length - 1].x = toNode.x + toNode.width / 2
          eventPos.push({
            x: toNode.x + toNode.width / 2,
            y:
              this.getNodePosFromBoreder(toNode, 'T').start.y > mouseLoc.y
                ? toNode.y
                : toNode.y + toNode.height,
          })
        }
      }
      //竖线
      else {
        //更新x坐标
        eventPos[0].x = mouseLoc.x
        eventPos[1].x = mouseLoc.x
        //更新y坐标
        if (
          this.getNodePosFromBoreder(fromNode, 'L').start.x > mouseLoc.x ||
          this.getNodePosFromBoreder(fromNode, 'R').start.x < mouseLoc.x
        ) {
          eventPos[0].y = fromNode.y + fromNode.height / 2
          eventPos.unshift({
            x:
              this.getNodePosFromBoreder(fromNode, 'R').start.x < mouseLoc.x
                ? fromNode.x + fromNode.width
                : fromNode.x,
            y: fromNode.y + fromNode.height / 2,
          })
        }
        if (
          this.getNodePosFromBoreder(toNode, 'L').start.x > mouseLoc.x ||
          this.getNodePosFromBoreder(toNode, 'R').start.x < mouseLoc.x
        ) {
          eventPos[eventPos.length - 1].y = toNode.y + toNode.height / 2
          eventPos.push({
            x:
              this.getNodePosFromBoreder(toNode, 'R').start.x < mouseLoc.x
                ? toNode.x + toNode.width
                : toNode.x,
            y: toNode.y + toNode.height / 2,
          })
        }
      }
    }
    //二折情况
    else if (posMaxIdx === 2) {
      //横线
      if (
        eventPos[eventObj.targetLineStartPointIdx].y ===
        eventPos[eventObj.targetLineStartPointIdx + 1].y
      ) {
        eventPos[eventObj.targetLineStartPointIdx].y = mouseLoc.y
        eventPos[eventObj.targetLineStartPointIdx + 1].y = mouseLoc.y
        //超出from节点边界
        if (
          (this.getNodePosFromBoreder(fromNode, 'B').start.y < mouseLoc.y ||
            this.getNodePosFromBoreder(fromNode, 'T').start.y > mouseLoc.y) &&
          eventObj.targetLineStartPointIdx === 0
        ) {
          eventPos[eventObj.targetLineStartPointIdx].x =
            fromNode.x + fromNode.width / 2
          eventPos.unshift({
            x: fromNode.x + fromNode.width / 2,
            y:
              this.getNodePosFromBoreder(fromNode, 'T').start.y > mouseLoc.y
                ? fromNode.y
                : fromNode.y + fromNode.height,
          })
        }
        //超出to节点边界
        if (
          (this.getNodePosFromBoreder(toNode, 'B').start.y < mouseLoc.y ||
            this.getNodePosFromBoreder(toNode, 'T').start.y > mouseLoc.y) &&
          eventObj.targetLineStartPointIdx === 1
        ) {
          eventPos[eventObj.targetLineStartPointIdx + 1].x =
            toNode.x + toNode.width / 2
          eventPos.push({
            x: toNode.x + toNode.width / 2,
            y:
              this.getNodePosFromBoreder(toNode, 'T').start.y > mouseLoc.y
                ? toNode.y
                : toNode.y + toNode.height,
          })
        }
        //进入from内
        if (
          eventObj.targetLineStartPointIdx === 1 &&
          this.getNodePosFromBoreder(fromNode, 'B').start.y > mouseLoc.y &&
          this.getNodePosFromBoreder(fromNode, 'T').start.y < mouseLoc.y
        ) {
          // console.log('from内')
          eventPos.splice(0, 1)
          eventPos[0].x =
            toNode.x > fromNode.x ? fromNode.x + fromNode.width : fromNode.x
        }
        //进入to内
        if (
          eventObj.targetLineStartPointIdx === 0 &&
          this.getNodePosFromBoreder(toNode, 'B').start.y > mouseLoc.y &&
          this.getNodePosFromBoreder(toNode, 'T').start.y < mouseLoc.y
        ) {
          // console.log('to内')
          eventPos.splice(eventPos.length - 1, 1)
          eventPos[eventPos.length - 1].x =
            fromNode.x > toNode.x ? toNode.x + toNode.width : toNode.x
        }
      }
      //竖线
      else {
        eventPos[eventObj.targetLineStartPointIdx].x = mouseLoc.x
        eventPos[eventObj.targetLineStartPointIdx + 1].x = mouseLoc.x
        //超出from节点边界
        if (
          (this.getNodePosFromBoreder(fromNode, 'L').start.x > mouseLoc.x ||
            this.getNodePosFromBoreder(fromNode, 'R').start.x < mouseLoc.x) &&
          eventObj.targetLineStartPointIdx === 0
        ) {
          // console.log('超出from')
          eventPos[eventObj.targetLineStartPointIdx].y =
            fromNode.y + fromNode.height / 2
          eventPos.unshift({
            x:
              this.getNodePosFromBoreder(fromNode, 'L').start.x > mouseLoc.x
                ? fromNode.x
                : fromNode.x + fromNode.width,
            y: fromNode.y + fromNode.height / 2,
          })
        }
        //超出to节点边界
        if (
          (this.getNodePosFromBoreder(toNode, 'L').start.x > mouseLoc.x ||
            this.getNodePosFromBoreder(toNode, 'R').start.x < mouseLoc.x) &&
          eventObj.targetLineStartPointIdx === 1
        ) {
          // console.log('超出to')
          eventPos[eventObj.targetLineStartPointIdx + 1].y =
            toNode.y + toNode.height / 2
          eventPos.push({
            x:
              this.getNodePosFromBoreder(toNode, 'L').start.x > mouseLoc.x
                ? toNode.x
                : toNode.x + toNode.width,
            y: toNode.y + toNode.height / 2,
          })
        }
        //进入from内
        if (
          eventObj.targetLineStartPointIdx === 1 &&
          this.getNodePosFromBoreder(fromNode, 'L').start.x < mouseLoc.x &&
          this.getNodePosFromBoreder(fromNode, 'R').start.x > mouseLoc.x
        ) {
          // console.log('from内')
          eventPos.splice(0, 1)
          eventPos[0].y =
            toNode.y > fromNode.y ? fromNode.y + fromNode.height : fromNode.y
        }
        //进入to内
        if (
          eventObj.targetLineStartPointIdx === 0 &&
          this.getNodePosFromBoreder(toNode, 'L').start.x < mouseLoc.x &&
          this.getNodePosFromBoreder(toNode, 'R').start.x > mouseLoc.x
        ) {
          // console.log('to内')
          eventPos.splice(eventPos.length - 1, 1)
          eventPos[eventPos.length - 1].y =
            fromNode.y > toNode.y ? toNode.y + toNode.height : toNode.y
        }
      }
    }
    //三折情况
    else if (posMaxIdx === 3) {
      //横线
      if (
        eventPos[eventObj.targetLineStartPointIdx].y ===
        eventPos[eventObj.targetLineStartPointIdx + 1].y
      ) {
        eventPos[eventObj.targetLineStartPointIdx].y = mouseLoc.y
        eventPos[eventObj.targetLineStartPointIdx + 1].y = mouseLoc.y
        //触发线段是挨着from节点
        if (eventObj.targetLineStartPointIdx === 0) {
          //Z形状
          if (judgeLineShapeIsZ(eventPos)) {
            //超出最高点
            if (
              eventPos[0].y < this.getNodePosFromBoreder(fromNode, 'T').start.y
            ) {
              eventPos[0] = {
                x:
                  this.getNodePosFromBoreder(fromNode, 'T').start.x +
                  fromNode.width / 2,
                y: fromNode.y,
              }
              eventPos[1] = { x: eventPos[0].x, y: mouseLoc.y }
              if (
                mouseLoc.y < this.getNodePosFromBoreder(toNode, 'T').start.y ||
                mouseLoc.y > this.getNodePosFromBoreder(toNode, 'B').start.y
              ) {
                eventPos[2] = {
                  x:
                    this.getNodePosFromBoreder(toNode, 'L').start.x +
                    toNode.width / 2,
                  y: mouseLoc.y,
                }
                eventPos[3] = {
                  x: eventPos[2].x,
                  y:
                    mouseLoc.y < this.getNodePosFromBoreder(toNode, 'T').start.y
                      ? this.getNodePosFromBoreder(toNode, 'T').start.y
                      : this.getNodePosFromBoreder(toNode, 'B').start.y,
                }
              } else {
                eventPos[2] = {
                  x:
                    eventPos[1].x <
                    this.getNodePosFromBoreder(toNode, 'R').start.x
                      ? this.getNodePosFromBoreder(toNode, 'L').start.x
                      : this.getNodePosFromBoreder(toNode, 'R').start.x,
                  y: mouseLoc.y,
                }
                eventPos.splice(3)
              }
            }
            //超出最低点
            else if (
              eventPos[0].y > this.getNodePosFromBoreder(fromNode, 'B').start.y
            ) {
              eventPos[0] = {
                x:
                  this.getNodePosFromBoreder(fromNode, 'T').start.x +
                  fromNode.width / 2,
                y: this.getNodePosFromBoreder(fromNode, 'B').start.y,
              }
              eventPos[1] = { x: eventPos[0].x, y: mouseLoc.y }
              if (
                mouseLoc.y < this.getNodePosFromBoreder(toNode, 'T').start.y ||
                mouseLoc.y > this.getNodePosFromBoreder(toNode, 'B').start.y
              ) {
                eventPos[2] = {
                  x:
                    this.getNodePosFromBoreder(toNode, 'L').start.x +
                    toNode.width / 2,
                  y: mouseLoc.y,
                }
                eventPos[3] = {
                  x: eventPos[2].x,
                  y:
                    mouseLoc.y < this.getNodePosFromBoreder(toNode, 'T').start.y
                      ? this.getNodePosFromBoreder(toNode, 'T').start.y
                      : this.getNodePosFromBoreder(toNode, 'B').start.y,
                }
              } else {
                eventPos[2] = {
                  x:
                    eventPos[1].x <
                    this.getNodePosFromBoreder(toNode, 'R').start.x
                      ? this.getNodePosFromBoreder(toNode, 'L').start.x
                      : this.getNodePosFromBoreder(toNode, 'R').start.x,
                  y: mouseLoc.y,
                }
                eventPos.splice(3)
              }
            }
          }
          //C形状
          else {
            if (
              mouseLoc.y < this.getNodePosFromBoreder(fromNode, 'T').start.y
            ) {
              eventPos[0].y = this.getNodePosFromBoreder(fromNode, 'T').start.y
              eventPos[1].y = this.getNodePosFromBoreder(fromNode, 'T').start.y
            } else if (
              mouseLoc.y > this.getNodePosFromBoreder(fromNode, 'B').start.y
            ) {
              eventPos[0].y = this.getNodePosFromBoreder(fromNode, 'B').start.y
              eventPos[1].y = this.getNodePosFromBoreder(fromNode, 'B').start.y
            }
          }
        }
        //触发线段是挨着to节点
        else if (eventObj.targetLineStartPointIdx === eventPos.length - 2) {
          //Z形状
          if (judgeLineShapeIsZ(eventPos)) {
            //超出最高点
            if (
              eventPos[3].y < this.getNodePosFromBoreder(toNode, 'T').start.y
            ) {
              eventPos[3] = {
                x:
                  this.getNodePosFromBoreder(toNode, 'T').start.x +
                  toNode.width / 2,
                y: toNode.y,
              }
              eventPos[2] = { x: eventPos[3].x, y: mouseLoc.y }
              if (
                mouseLoc.y <
                  this.getNodePosFromBoreder(fromNode, 'T').start.y ||
                mouseLoc.y > this.getNodePosFromBoreder(fromNode, 'B').start.y
              ) {
                eventPos[1] = {
                  x:
                    this.getNodePosFromBoreder(fromNode, 'L').start.x +
                    fromNode.width / 2,
                  y: mouseLoc.y,
                }
                eventPos[0] = {
                  x: eventPos[1].x,
                  y:
                    mouseLoc.y <
                    this.getNodePosFromBoreder(fromNode, 'T').start.y
                      ? this.getNodePosFromBoreder(fromNode, 'T').start.y
                      : this.getNodePosFromBoreder(fromNode, 'B').start.y,
                }
              } else {
                eventPos[1] = {
                  x:
                    eventPos[2].x >
                    this.getNodePosFromBoreder(fromNode, 'R').start.x
                      ? this.getNodePosFromBoreder(fromNode, 'R').start.x
                      : this.getNodePosFromBoreder(fromNode, 'L').start.x,
                  y: mouseLoc.y,
                }
                eventPos.splice(0, 1)
              }
            }
            //超出最低点
            else if (
              eventPos[3].y > this.getNodePosFromBoreder(toNode, 'B').start.y
            ) {
              eventPos[3] = {
                x:
                  this.getNodePosFromBoreder(toNode, 'L').start.x +
                  toNode.width / 2,
                y: this.getNodePosFromBoreder(toNode, 'B').start.y,
              }
              eventPos[2] = { x: eventPos[3].x, y: mouseLoc.y }
              if (
                mouseLoc.y <
                  this.getNodePosFromBoreder(fromNode, 'T').start.y ||
                mouseLoc.y > this.getNodePosFromBoreder(fromNode, 'B').start.y
              ) {
                eventPos[1] = {
                  x:
                    this.getNodePosFromBoreder(fromNode, 'L').start.x +
                    fromNode.width / 2,
                  y: mouseLoc.y,
                }
                eventPos[0] = {
                  x: eventPos[1].x,
                  y:
                    mouseLoc.y <
                    this.getNodePosFromBoreder(fromNode, 'T').start.y
                      ? this.getNodePosFromBoreder(fromNode, 'T').start.y
                      : this.getNodePosFromBoreder(fromNode, 'B').start.y,
                }
              } else {
                eventPos[1] = {
                  x:
                    eventPos[2].x >
                    this.getNodePosFromBoreder(fromNode, 'R').start.x
                      ? this.getNodePosFromBoreder(fromNode, 'R').start.x
                      : this.getNodePosFromBoreder(fromNode, 'L').start.x,
                  y: mouseLoc.y,
                }
                eventPos.splice(0, 1)
              }
            }
          }
          //C形状
          else {
            if (mouseLoc.y < this.getNodePosFromBoreder(toNode, 'T').start.y) {
              eventPos[2].y = this.getNodePosFromBoreder(toNode, 'T').start.y
              eventPos[3].y = this.getNodePosFromBoreder(toNode, 'T').start.y
            } else if (
              mouseLoc.y > this.getNodePosFromBoreder(toNode, 'B').start.y
            ) {
              eventPos[2].y = this.getNodePosFromBoreder(toNode, 'B').start.y
              eventPos[3].y = this.getNodePosFromBoreder(toNode, 'B').start.y
            }
          }
        }
        //触发线段是中间部位
        else {
          //from变化
          if (
            mouseLoc.y > this.getNodePosFromBoreder(fromNode, 'T').start.y &&
            mouseLoc.y < this.getNodePosFromBoreder(fromNode, 'B').start.y
          ) {
            eventPos.splice(0, 1)
            eventPos[0].x =
              eventPos[1].x < this.getNodePosFromBoreder(fromNode, 'L').start.x
                ? this.getNodePosFromBoreder(fromNode, 'L').start.x
                : this.getNodePosFromBoreder(fromNode, 'R').start.x
          }
          //to变化
          if (
            mouseLoc.y > this.getNodePosFromBoreder(toNode, 'T').start.y &&
            mouseLoc.y < this.getNodePosFromBoreder(toNode, 'B').start.y
          ) {
            eventPos[eventPos.length - 2].x =
              eventPos[eventPos.length - 3].x <
              this.getNodePosFromBoreder(toNode, 'L').start.x
                ? this.getNodePosFromBoreder(toNode, 'L').start.x
                : this.getNodePosFromBoreder(toNode, 'R').start.x
            eventPos.splice(eventPos.length - 1)
          }
        }
      }
      //竖线
      else {
        eventPos[eventObj.targetLineStartPointIdx].x = mouseLoc.x
        eventPos[eventObj.targetLineStartPointIdx + 1].x = mouseLoc.x

        //触发线段是挨着from节点
        if (eventObj.targetLineStartPointIdx === 0) {
          // console.log('from节点')
          // Z形状
          if (judgeLineShapeIsN(eventPos)) {
            //超出最左点
            if (
              eventPos[0].x < this.getNodePosFromBoreder(fromNode, 'L').start.x
            ) {
              eventPos[0] = {
                x: this.getNodePosFromBoreder(fromNode, 'L').start.x,
                y:
                  this.getNodePosFromBoreder(fromNode, 'L').start.y +
                  fromNode.height / 2,
              }
              eventPos[1] = { x: mouseLoc.x, y: eventPos[0].y }
              if (
                mouseLoc.x < this.getNodePosFromBoreder(toNode, 'L').start.x ||
                mouseLoc.x > this.getNodePosFromBoreder(toNode, 'R').start.x
              ) {
                eventPos[2] = {
                  x: mouseLoc.x,
                  y:
                    this.getNodePosFromBoreder(toNode, 'T').start.y +
                    toNode.height / 2,
                }
                eventPos[3] = {
                  x:
                    mouseLoc.x < this.getNodePosFromBoreder(toNode, 'L').start.x
                      ? this.getNodePosFromBoreder(toNode, 'L').start.x
                      : this.getNodePosFromBoreder(toNode, 'R').start.x,
                  y: eventPos[2].y,
                }
              } else {
                eventPos[2] = {
                  x: mouseLoc.x,
                  y:
                    eventPos[1].y <
                    this.getNodePosFromBoreder(toNode, 'T').start.y
                      ? this.getNodePosFromBoreder(toNode, 'T').start.y
                      : this.getNodePosFromBoreder(toNode, 'B').start.y,
                }
                eventPos.splice(3)
              }
            }
            //超出最右点
            else if (
              eventPos[0].x > this.getNodePosFromBoreder(fromNode, 'R').start.x
            ) {
              eventPos[0] = {
                x: this.getNodePosFromBoreder(fromNode, 'R').start.x,
                y:
                  this.getNodePosFromBoreder(fromNode, 'R').start.y +
                  fromNode.height / 2,
              }
              eventPos[1] = { x: mouseLoc.x, y: eventPos[0].y }
              if (
                mouseLoc.x < this.getNodePosFromBoreder(toNode, 'L').start.x ||
                mouseLoc.x > this.getNodePosFromBoreder(toNode, 'R').start.x
              ) {
                eventPos[2] = {
                  x: mouseLoc.x,
                  y:
                    this.getNodePosFromBoreder(toNode, 'T').start.y +
                    toNode.height / 2,
                }
                eventPos[3] = {
                  x:
                    mouseLoc.x < this.getNodePosFromBoreder(toNode, 'L').start.x
                      ? this.getNodePosFromBoreder(toNode, 'L').start.x
                      : this.getNodePosFromBoreder(toNode, 'R').start.x,
                  y: eventPos[2].y,
                }
              } else {
                eventPos[2] = {
                  x: mouseLoc.x,
                  y:
                    eventPos[1].y <
                    this.getNodePosFromBoreder(toNode, 'T').start.y
                      ? this.getNodePosFromBoreder(toNode, 'T').start.y
                      : this.getNodePosFromBoreder(toNode, 'B').start.y,
                }
                eventPos.splice(3)
              }
            }
          }
          //C形状
          else {
            if (
              mouseLoc.x < this.getNodePosFromBoreder(fromNode, 'L').start.x
            ) {
              eventPos[0].x =
                this.getNodePosFromBoreder(fromNode, 'L').start.x + 1
              eventPos[1].x =
                this.getNodePosFromBoreder(fromNode, 'L').start.x + 1
            } else if (
              mouseLoc.x > this.getNodePosFromBoreder(fromNode, 'R').start.x
            ) {
              eventPos[0].x =
                this.getNodePosFromBoreder(fromNode, 'R').start.x - 1
              eventPos[1].x =
                this.getNodePosFromBoreder(fromNode, 'R').start.x - 1
            }
          }
        }
        //触发线段是挨着to节点
        else if (eventObj.targetLineStartPointIdx === eventPos.length - 2) {
          // console.log('to节点')
          //Z形状
          if (judgeLineShapeIsN(eventPos)) {
            //超出最左点
            if (
              eventPos[3].x < this.getNodePosFromBoreder(toNode, 'L').start.x
            ) {
              eventPos[3] = {
                x: this.getNodePosFromBoreder(toNode, 'L').start.x,
                y:
                  this.getNodePosFromBoreder(toNode, 'L').start.y +
                  toNode.height / 2,
              }
              eventPos[2] = { x: mouseLoc.x, y: eventPos[3].y }
              //超出对面节点
              if (
                mouseLoc.x <
                  this.getNodePosFromBoreder(fromNode, 'L').start.x ||
                mouseLoc.x > this.getNodePosFromBoreder(fromNode, 'R').start.x
              ) {
                eventPos[1] = {
                  x: mouseLoc.x,
                  y:
                    this.getNodePosFromBoreder(fromNode, 'T').start.y +
                    fromNode.height / 2,
                }
                eventPos[0] = {
                  x:
                    mouseLoc.x <
                    this.getNodePosFromBoreder(fromNode, 'L').start.x
                      ? this.getNodePosFromBoreder(fromNode, 'L').start.x
                      : this.getNodePosFromBoreder(fromNode, 'R').start.x,
                  y: eventPos[1].y,
                }
              } else {
                eventPos[1] = {
                  x: mouseLoc.x,
                  y:
                    eventPos[2].y >
                    this.getNodePosFromBoreder(fromNode, 'B').start.y
                      ? this.getNodePosFromBoreder(fromNode, 'B').start.y
                      : this.getNodePosFromBoreder(fromNode, 'T').start.y,
                }
                eventPos.splice(0, 1)
              }
            }
            //超出最右点
            else if (
              eventPos[3].x > this.getNodePosFromBoreder(toNode, 'R').start.x
            ) {
              eventPos[3] = {
                x: this.getNodePosFromBoreder(toNode, 'R').start.x,
                y:
                  this.getNodePosFromBoreder(toNode, 'R').start.y +
                  toNode.height / 2,
              }
              eventPos[2] = { x: mouseLoc.x, y: eventPos[3].y }
              //超出对面节点
              if (
                mouseLoc.x <
                  this.getNodePosFromBoreder(fromNode, 'L').start.x ||
                mouseLoc.x > this.getNodePosFromBoreder(fromNode, 'R').start.x
              ) {
                eventPos[1] = {
                  x: mouseLoc.x,
                  y:
                    this.getNodePosFromBoreder(fromNode, 'T').start.y +
                    fromNode.height / 2,
                }
                eventPos[0] = {
                  x:
                    mouseLoc.x <
                    this.getNodePosFromBoreder(fromNode, 'L').start.x
                      ? this.getNodePosFromBoreder(fromNode, 'L').start.x
                      : this.getNodePosFromBoreder(fromNode, 'R').start.x,
                  y: eventPos[1].y,
                }
              } else {
                eventPos[1] = {
                  x: mouseLoc.x,
                  y:
                    eventPos[2].y >
                    this.getNodePosFromBoreder(fromNode, 'B').start.y
                      ? this.getNodePosFromBoreder(fromNode, 'B').start.y
                      : this.getNodePosFromBoreder(fromNode, 'T').start.y,
                }
                eventPos.splice(0, 1)
              }
            }
          }
          //C形状
          else {
            if (mouseLoc.x < this.getNodePosFromBoreder(toNode, 'L').start.x) {
              eventPos[2].x =
                this.getNodePosFromBoreder(toNode, 'L').start.x + 1
              eventPos[3].x =
                this.getNodePosFromBoreder(toNode, 'L').start.x + 1
            } else if (
              mouseLoc.x > this.getNodePosFromBoreder(toNode, 'R').start.x
            ) {
              eventPos[2].x =
                this.getNodePosFromBoreder(toNode, 'R').start.x - 1
              eventPos[3].x =
                this.getNodePosFromBoreder(toNode, 'R').start.x - 1
            }
          }
        }
        //触发线段是中间部位
        else {
          // console.log('中间')
          //from变化
          if (
            mouseLoc.x > this.getNodePosFromBoreder(fromNode, 'L').start.x &&
            mouseLoc.x < this.getNodePosFromBoreder(fromNode, 'R').start.x
          ) {
            eventPos.splice(0, 1)
            eventPos[0].y =
              eventPos[1].y < this.getNodePosFromBoreder(fromNode, 'T').start.y
                ? this.getNodePosFromBoreder(fromNode, 'T').start.y
                : this.getNodePosFromBoreder(fromNode, 'B').start.y
          }
          //to变化
          if (
            mouseLoc.x > this.getNodePosFromBoreder(toNode, 'L').start.x &&
            mouseLoc.x < this.getNodePosFromBoreder(toNode, 'R').start.x
          ) {
            eventPos[eventPos.length - 2].y =
              eventPos[eventPos.length - 3].y <
              this.getNodePosFromBoreder(toNode, 'T').start.y
                ? this.getNodePosFromBoreder(toNode, 'T').start.y
                : this.getNodePosFromBoreder(toNode, 'B').start.y
            eventPos.splice(eventPos.length - 1)
          }
        }
      }

      //判断是否需要三折合并一折
      if (
        eventPos.length === 4 &&
        eventPos[1].x === eventPos[2].x &&
        eventPos[1].y === eventPos[2].y
      ) {
        eventPos.splice(1, 2)
      }
    }

    //判断连线形状是不是Z形状（false为C）
    function judgeLineShapeIsZ(eventPos) {
      let judgeWidth = 0
      for (let i = 0, len = eventPos.length - 1; i < len; i++) {
        judgeWidth += Math.abs(eventPos[i].x - eventPos[i + 1].x)
      }

      return (
        judgeWidth === Math.abs(eventPos[0].x - eventPos[eventPos.length - 1].x)
      )
    }
    //判断连线形状是不是N形状（false为|__|）
    function judgeLineShapeIsN(eventPos) {
      let judgeHeight = 0
      for (let i = 0, len = eventPos.length - 1; i < len; i++) {
        judgeHeight += Math.abs(eventPos[i].y - eventPos[i + 1].y)
      }

      return (
        judgeHeight ===
        Math.abs(eventPos[0].y - eventPos[eventPos.length - 1].y)
      )
    }

    this.emit(LineEvent.Move, eventObj.target)
  }

  //新增结点
  addNode() {
    this.editType = EditType.addNote
  }

  /**
   * 包装结点信息方法
   * @param {Object} addNodeDefaultAttr 默认信息
   * @param {Object} mouseLoc 当前鼠标位置
   */
  packNodeInfo(addNodeDefaultAttr, mouseLoc) {
    const { x, y } = this.transLoc(mouseLoc)

    const newNode = Object.assign(addNodeDefaultAttr, {
      x,
      y: y + 24,
      width: 50,
      height: 50,
    })

    return newNode
  }

  /**
   * 获取与鼠标位置重合的实体
   * @param {*} mouseLoc
   */
  getOverlapNode() {
    const data = this.dataHandler.originData

    //查看是否节点触发
    for (let i = 0, len = data.nodeDataArray.length; i < len; i++) {
      const w = Number(data.nodeDataArray[i].width) || 100
      const h = Number(data.nodeDataArray[i].height) || 50
      const x = Number(data.nodeDataArray[i].x)
      const y = Number(data.nodeDataArray[i].y)

      if (x > x && x < w + x && y > y && y < h + y) {
        return data.nodeDataArray[i]
      }
    }
  }

  /**
   * 根据鼠标所在位置，换算出相对于完整绘图区的坐标
   * @param {object} mouseLoc 鼠标在canvas上的位置
   */
  transLoc(mouseLoc) {
    const translatePos = this.canvas.getTranslate()
    const zoomRate = this.canvas.zoomRate
    const evX = Math.floor((mouseLoc.x + translatePos.x) / zoomRate),
      evY = Math.floor((mouseLoc.y + translatePos.y) / zoomRate)

    return {
      x: evX,
      y: evY,
    }
  }

  //新增连线
  addLine(lineType) {
    this.editType = lineType
  }

  //新增框
  addMark() {
    this.editType = EditType.addMark
  }

  //新增备注
  addNote() {
    this.editType = EditType.addNote
  }

  // 新增标注信息
  addRemark(remarkStr = '') {
    this.focusTarget?.forEach((focusItem) => {
      // eslint-disable-next-line @typescript-eslint/ban-ts-comment
      // @ts-ignore
      focusItem.remark = remarkStr
    })

    this.update()
  }

  //改变颜色
  changeColor(value, type) {
    this.focusTarget?.forEach((focusItem) => {
      //实体
      if (focusItem instanceof Node) {
        if (type !== 'color') {
          focusItem[type] = value
        } else {
          console.log('修改的是属性文字颜色, 逻辑待完善.')
        }
      }
      //框
      else if (focusItem instanceof NodeRect) {
        focusItem[type] = value
      }
      //备注文本
      else if (focusItem instanceof TextBase) {
        // eslint-disable-next-line @typescript-eslint/ban-ts-comment
        // @ts-ignore
        focusItem.textColor = value
      }
    })
    this.update()
  }

  /**
   * 根据chooseTargets数据创建新的连线信息数据
   * @param {String} lineType     必选，创建的线型
   * @param {Object} defaultData  可选，带有默认数据的线，当该参数有值证明是批量建线
   */
  createLineInfo(lineType, defaultData) {
    const chooseTargets = this.chooseTargets,
      chooseTargets0Loc = this.getNodePosFromBoreder(chooseTargets[0], 'L'),
      chooseTargets1Loc = this.getNodePosFromBoreder(chooseTargets[1], 'L')

    const pos = []
    //起始节点和终止节点是同一个
    if (chooseTargets[0].id === chooseTargets[1].id) {
      pos[0] = {
        x: chooseTargets[0].x + chooseTargets[0].width,
        y: chooseTargets[0].y + chooseTargets[0].height - 20,
      }
      pos[1] = {
        x: chooseTargets[0].x + chooseTargets[0].width + 20,
        y: chooseTargets[0].y + chooseTargets[0].height - 20,
      }
      pos[2] = {
        x: chooseTargets[0].x + chooseTargets[0].width + 20,
        y: chooseTargets[0].y + chooseTargets[0].height + 20,
      }
      pos[3] = {
        x: chooseTargets[0].x + chooseTargets[0].width - 20,
        y: chooseTargets[0].y + chooseTargets[0].height + 20,
      }
      pos[4] = {
        x: chooseTargets[0].x + chooseTargets[0].width - 20,
        y: chooseTargets[0].y + chooseTargets[0].height,
      }
    }
    //起始节点是分支节点
    else if (['exclusive', 'inclusive'].includes(chooseTargets[0].type)) {
      //分支上 to下 -> N形状
      if (chooseTargets0Loc.end.y < chooseTargets1Loc.start.y) {
        pos[0] = {
          x: chooseTargets0Loc.end.x + Math.floor(chooseTargets[0].width / 2),
          y: chooseTargets0Loc.end.y,
        }
        pos[1] = {
          x: pos[0].x,
          y:
            pos[0].y +
            Math.floor(
              (chooseTargets1Loc.start.y - chooseTargets0Loc.end.y) / 2,
            ),
        }
        pos[2] = {
          x: chooseTargets1Loc.start.x + Math.floor(chooseTargets[1].width / 2),
          y: pos[1].y,
        }
        pos[3] = { x: pos[2].x, y: chooseTargets1Loc.start.y }
      }
      //分支下 to上 -> U形状
      else {
        pos[0] = {
          x: chooseTargets0Loc.end.x + Math.floor(chooseTargets[0].width / 2),
          y: chooseTargets0Loc.end.y,
        }
        pos[1] = {
          x: pos[0].x,
          y: Math.max(chooseTargets0Loc.end.y, chooseTargets1Loc.start.y) + 20,
        }
        pos[2] = {
          x: chooseTargets1Loc.start.x + Math.floor(chooseTargets[1].width / 2),
          y: pos[1].y,
        }
        pos[3] = { x: pos[2].x, y: chooseTargets1Loc.end.y }
      }
    }
    //终止节点是分支节点
    else if (['exclusive', 'inclusive'].includes(chooseTargets[1].type)) {
      //from上 分支下
      if (chooseTargets0Loc.end.y < chooseTargets1Loc.start.y) {
        pos[0] = {
          x: chooseTargets0Loc.end.x + Math.floor(chooseTargets[0].width / 2),
          y: chooseTargets0Loc.end.y,
        }
        pos[1] = {
          x: pos[0].x,
          y:
            pos[0].y +
            Math.floor(
              (chooseTargets1Loc.start.y - chooseTargets0Loc.end.y) / 2,
            ),
        }
        pos[2] = {
          x: chooseTargets1Loc.start.x + Math.floor(chooseTargets[1].width / 2),
          y: pos[1].y,
        }
        pos[3] = { x: pos[2].x, y: chooseTargets1Loc.start.y }
      }
      //from下分支上
      else {
        pos[0] = {
          x: chooseTargets0Loc.end.x + Math.floor(chooseTargets[0].width / 2),
          y: chooseTargets0Loc.start.y,
        }
        pos[1] = {
          x: pos[0].x,
          y: Math.min(chooseTargets0Loc.end.y, chooseTargets1Loc.start.y) - 20,
        }
        pos[2] = {
          x: chooseTargets1Loc.start.x + Math.floor(chooseTargets[1].width / 2),
          y: pos[1].y,
        }
        pos[3] = { x: pos[2].x, y: chooseTargets1Loc.start.y }
      }
    }
    //起始节点是普通节点
    else {
      //from左  to右
      if (
        this.getNodePosFromBoreder(chooseTargets[0], 'R').start.x <
        this.getNodePosFromBoreder(chooseTargets[1], 'L').start.x
      ) {
        //有重合 -> 直线
        if (
          (chooseTargets1Loc.start.y - chooseTargets0Loc.end.y) *
            (chooseTargets1Loc.end.y - chooseTargets0Loc.start.y) <
          0
        ) {
          // console.log('有重合')
          const y =
            Math.max(chooseTargets0Loc.start.y, chooseTargets1Loc.start.y) +
            Math.abs(
              Math.floor(
                (Math.min(chooseTargets0Loc.end.y, chooseTargets1Loc.end.y) -
                  Math.max(
                    chooseTargets0Loc.start.y,
                    chooseTargets1Loc.start.y,
                  )) /
                  2,
              ),
            )
          pos[0] = {
            x: this.getNodePosFromBoreder(chooseTargets[0], 'R').start.x,
            y: y,
          }
          pos[1] = {
            x: this.getNodePosFromBoreder(chooseTargets[1], 'L').start.x,
            y: y,
          }
        }
        //无重合 -> 二折
        else {
          // console.log('无重合')
          //from下  to上
          if (chooseTargets0Loc.start.y > chooseTargets1Loc.end.y) {
            pos[0] = {
              x:
                chooseTargets0Loc.start.x +
                Math.floor(chooseTargets[0].width / 2),
              y: chooseTargets0Loc.start.y,
            }
            pos[1] = {
              x: pos[0].x,
              y:
                chooseTargets1Loc.start.y +
                Math.floor(chooseTargets[1].height / 2),
            }
            pos[2] = { x: chooseTargets1Loc.start.x, y: pos[1].y }
          }
          //from上  to下
          else {
            pos[0] = {
              x: this.getNodePosFromBoreder(chooseTargets[0], 'R').start.x,
              y:
                chooseTargets0Loc.start.y +
                Math.floor(chooseTargets[0].height / 2),
            }
            pos[1] = {
              x:
                this.getNodePosFromBoreder(chooseTargets[1], 'L').start.x +
                Math.floor(chooseTargets[1].width / 2),
              y: pos[0].y,
            }
            pos[2] = {
              x: pos[1].x,
              y: this.getNodePosFromBoreder(chooseTargets[1], 'L').start.y,
            }
          }
        }
      }
      //from右  to左
      else if (
        this.getNodePosFromBoreder(chooseTargets[0], 'L').start.x >
        this.getNodePosFromBoreder(chooseTargets[1], 'R').start.x
      ) {
        //有重合 -> 直线
        if (
          (chooseTargets1Loc.start.y - chooseTargets0Loc.end.y) *
            (chooseTargets1Loc.end.y - chooseTargets0Loc.start.y) <
          0
        ) {
          // console.log('有重合')
          const y =
            Math.max(chooseTargets0Loc.start.y, chooseTargets1Loc.start.y) +
            Math.abs(
              Math.floor(
                (Math.min(chooseTargets0Loc.end.y, chooseTargets1Loc.end.y) -
                  Math.max(
                    chooseTargets0Loc.start.y,
                    chooseTargets1Loc.start.y,
                  )) /
                  2,
              ),
            )
          pos[0] = {
            x: this.getNodePosFromBoreder(chooseTargets[0], 'L').start.x,
            y: y,
          }
          pos[1] = {
            x: this.getNodePosFromBoreder(chooseTargets[1], 'R').start.x,
            y: y,
          }
        }
        //无重合 -> 二折
        else {
          // console.log('无重合')
          //from下  to上
          if (chooseTargets0Loc.start.y > chooseTargets1Loc.end.y) {
            pos[0] = {
              x:
                chooseTargets0Loc.start.x +
                Math.floor(chooseTargets[0].width / 2),
              y: chooseTargets0Loc.start.y,
            }
            pos[1] = {
              x: pos[0].x,
              y:
                chooseTargets1Loc.start.y +
                Math.floor(chooseTargets[1].height / 2),
            }
            pos[2] = {
              x: this.getNodePosFromBoreder(chooseTargets[1], 'R').start.x,
              y: pos[1].y,
            }
          }
          //from上  to下
          else {
            pos[0] = {
              x: this.getNodePosFromBoreder(chooseTargets[0], 'L').start.x,
              y:
                chooseTargets0Loc.start.y +
                Math.floor(chooseTargets[0].height / 2),
            }
            pos[1] = {
              x:
                this.getNodePosFromBoreder(chooseTargets[1], 'L').start.x +
                Math.floor(chooseTargets[1].width / 2),
              y: pos[0].y,
            }
            pos[2] = {
              x: pos[1].x,
              y: this.getNodePosFromBoreder(chooseTargets[1], 'L').start.y,
            }
          }
        }
      }
      //from下 to上 上下非重叠重合 -> 直线
      else if (chooseTargets0Loc.start.y > chooseTargets1Loc.end.y) {
        const startX = Math.max(
          chooseTargets0Loc.start.x,
          chooseTargets1Loc.start.x,
        )
        const endX = Math.min(
          this.getNodePosFromBoreder(chooseTargets[0], 'R').start.x,
          this.getNodePosFromBoreder(chooseTargets[1], 'R').start.x,
        )
        const x = startX + Math.floor((endX - startX) / 2)

        pos[0] = { x: x, y: chooseTargets0Loc.start.y }
        pos[1] = { x: x, y: chooseTargets1Loc.end.y }
      }
      //from上 to下&重叠在一起
      else {
        const startX = Math.max(
          chooseTargets0Loc.start.x,
          chooseTargets1Loc.start.x,
        )
        const endX = Math.min(
          this.getNodePosFromBoreder(chooseTargets[0], 'R').start.x,
          this.getNodePosFromBoreder(chooseTargets[1], 'R').start.x,
        )
        const x = startX + Math.floor((endX - startX) / 2)

        pos[0] = { x: x, y: chooseTargets0Loc.end.y }
        pos[1] = { x: x, y: chooseTargets1Loc.start.y }
      }
    }

    //生成完整的线信息，通知外部更新数据
    if (pos.length) {
      const newLine = {}

      //没有线的默认数据 -> 创建单根线情况
      if (!defaultData) {
        Object.assign(newLine, {
          id: this.createKey(),
          from: chooseTargets[0].id,
          to: chooseTargets[1].id,
          pos: pos,
          type: lineType,
        })
      }
      //有线的默认数据 -> 批量创建线的情况
      else {
        defaultData.pos = pos
        Object.assign(newLine, defaultData)
      }

      return newLine
    }
    //线信息生成失败
    else {
      console.error('createLine方法错误：未生成对应关键点坐标')
    }
  }

  /**
   * 根据chooseTargets数据创建新的连线
   * @param {String} lineType     必选，创建的线型
   * @param {Object} defaultData  可选，带有默认数据的线，当该参数有值证明是批量建线
   */
  createLine(lineType, defaultData?) {
    const newLineInfo = this.createLineInfo(lineType, defaultData)

    // eslint-disable-next-line @typescript-eslint/ban-ts-comment
    // @ts-ignore 过滤器连线不调用外部接口
    if (['primaryKey', 'foreignKey'].includes(lineType))
      this.externalFn.addLine(newLineInfo)
  }

  mergeLine(lineType, defaultData) {
    const newLineInfo = this.createLineInfo(lineType, defaultData)

    return Object.assign(defaultData, newLineInfo)
  }

  //根据chooseTargets数据创建新的节点连线
  createBranchLine() {
    const chooseTargets = [].concat(this.chooseTargets),
      chooseTargets0Loc = this.getNodePosFromBoreder(chooseTargets[0], 'L'),
      chooseTargets1Loc = this.getNodePosFromBoreder(chooseTargets[1], 'L')

    // 终止节点是过滤器的: 不允许创建
    if (['inclusive', 'exclusive'].includes(chooseTargets[1].type)) {
      this.$message({
        message: '未将对象引用设置到对象的实例。',
        type: 'error',
        showClose: true,
      })
    }
    // 开始节点是过滤器的: 只创建一条fromFilter线
    else if (['inclusive', 'exclusive'].includes(chooseTargets[0].type)) {
      // 来自于过滤器(下行线)
      const fromFilter = this.createLine('fromFilterPrimaryKey') //目前没有fromFilterForeignKey的情况

      // eslint-disable-next-line @typescript-eslint/ban-ts-comment
      // @ts-ignore
      this.externalFn.addFilter({
        fromFilter,
      })
    }
    // 开始节点不是过滤器的: 正常的创建逻辑->两根线,一个过滤器
    else {
      //from上 to下 且间距>50（因为branch分支块的高度是40）-> 取两块中点放置分支
      const loc =
        chooseTargets0Loc.end.y + 50 < chooseTargets1Loc.start.y
          ? {
              x:
                chooseTargets0Loc.start.x +
                Math.floor(chooseTargets[0].width / 2) -
                20,
              y:
                chooseTargets0Loc.end.y +
                Math.floor(
                  (chooseTargets1Loc.start.y - chooseTargets0Loc.end.y) / 2,
                ) -
                10,
            }
          : {
              x:
                chooseTargets1Loc.start.x +
                Math.floor(chooseTargets[1].width / 2) -
                20,
              y:
                Math.min(chooseTargets0Loc.start.y, chooseTargets1Loc.start.y) -
                50,
            }
      //生成过滤器节点
      const newBranchFilter = {
        id: this.createKey(),
        x: loc.x,
        y: loc.y,
        width: 40,
        height: 20,
        type: 'exclusive',
        fromKey: chooseTargets[0].id,
      }

      // 链接到过滤器(上行线)
      this.chooseTargets = [chooseTargets[0], newBranchFilter]
      const toFilter = this.createLine('toFilter')

      // 来自于过滤器(下行线)
      this.chooseTargets = [newBranchFilter, chooseTargets[1]]
      const fromFilter = this.createLine('fromFilterPrimaryKey') //目前没有fromFilterForeignKey的情况

      // eslint-disable-next-line @typescript-eslint/ban-ts-comment
      // @ts-ignore
      this.externalFn.addFilter({
        filter: newBranchFilter,
        toFilter,
        fromFilter,
      })

      this.chooseTargets = []
    }
  }

  //根据key找到对应的节点
  getNodeWithKey(key) {
    for (const item of this.dataHandler.originData.nodeDataArray) {
      if (item.id === key) {
        return item
      }
    }
  }

  //批量创建连线
  createLines(lineList) {
    /**
     * lineList = [{id: 1234, from: 1, to: 2}]
     */
    for (const item of lineList) {
      const fromNode = this.getNodeWithKey(item.from)
      const toNode = this.getNodeWithKey(item.to)
      if (fromNode && toNode) {
        //目前没考虑过滤器的情况
        this.chooseTargets = [fromNode, toNode]
        this.createLine(item.type, item)
      }
    }
    this.chooseTargets = []
    this.update()
  }

  //批量创建连线
  mergeLines(lineList) {
    /**
     * lineList = [{id: 1234, from: 1, to: 2}]
     */
    const resultLines = []
    for (const item of lineList) {
      const fromNode = this.getNodeWithKey(item.from)
      const toNode = this.getNodeWithKey(item.to)
      if (fromNode && toNode) {
        //目前没考虑过滤器的情况
        this.chooseTargets = [fromNode, toNode]
        resultLines.push(this.mergeLine(item.type, item))
      }
    }
    this.chooseTargets = []

    return resultLines
  }

  //生成最大key方法（目前未做重复性判断）
  createKey() {
    //格式：时间戳 + 随机数
    return +new Date() + '' + Math.floor(Math.random() * 100000)
  }

  //封装提示信息
  $message(val) {
    // eslint-disable-next-line @typescript-eslint/ban-ts-comment
    // @ts-ignore
    this.externalFn.$message && this.externalFn.$message(val)
  }
}
