import { getTextWidth } from '../../../../core/utils/string'

export const getAutoSizeValue = (node, fontSize, fontFamily, lineHeight) => {
  return {
    width: Math.floor(
      Math.max(
        ...(node.attributes || []).map((it) =>
          getTextWidth(it.name, fontSize, fontFamily),
        ),
        80,
      ) + 20,
    ),
    height: Math.floor(
      Math.max(((node.attributes || []).length + 1) * lineHeight, 50),
    ),
  }
}
