<script lang="ts" setup>
import {reactive, onBeforeUnmount, onMounted, watch, onUnmounted} from 'vue'
import {ElButton, ElDivider, ElColorPicker} from 'element-plus'
import {Erg as Graph} from '@ej/erg-renderer/src'
import {defaultData as data} from './mock'
import {rightMenu} from './right-menu'
import Icons from './assets/icon/symbol-defs.svg'

const props = withDefaults(defineProps<{
  useThumbnail: boolean,
  useRightMenu: boolean,
  useAttrFocus: boolean,
  editable: string,
}>(), {
  useThumbnail: true,
  useRightMenu: true,
  useAttrFocus: true,
  editable: 'edit',
})

let graph

const state = reactive({
  scale: 100,
  bgC: '',
  bdC: '',
  color: '',
  lock: 'edit',
})

const mountGraph = () => {
  // const el = document.getElementById('canvas')
  graph = new Graph({
    el: document.getElementById('canvas'),
    fontFamily: '-apple-system, Noto Sans, Helvetica Neue, Helvetica, Nimbus Sans L, Arial, Liberation Sans, PingFang SC, Hiragino Sans GB, Noto Sans CJK SC, Source Han Sans SC, Source Han Sans CN, Microsoft YaHei, Wenquanyi Micro Hei, WenQuanYi Zen Hei, ST Heiti, SimHei, WenQuanYi Zen Hei Sharp, sans-serif',
    editable: state.lock,
    externalFn: {
      addNode: function (obj) {
        const str = prompt('请输入表名：')
        if (!str) return
        obj.name = str
        data.nodeDataArray.push(obj)
      },
      delNode: function (key) {
        console.log(key)
      },
      addLine: function (obj) {
        console.log('添加线了')
        obj.numLineTypes = 2
        data.linkDataArray.push(obj)
      },
      delLine: function (key) {
        console.log(key)
      },
      addFilter: function (obj) {
        obj.filter && data.nodeDataArray.push(obj.filter)
        obj.toFilter && data.linkDataArray.push(obj.toFilter)
        data.linkDataArray.push(obj.fromFilter)
      },
      delFilter: function (key) {
        console.log(key)
      },
      addMark: function (obj) {
        data.markDataArray.push(obj)
        markMap.set(obj.id, obj)
      },
      delMark: function (key) {
        console.log(key)
      },
      addNote: function (obj) {
        const str = prompt('请输入备注内容：')
        if (!str) return
        obj.text = str
        data.noteDataArray.push(obj)
      },
      delNote: function (key) {
        console.log(key)
      },
      $message: function (val) {
        console.log(val.message)
      },
      handleCopy(ev1, ev2) {
        console.log(ev1, ev2)
        return true
      },
      handlePaste(nodes) {
        data.nodeDataArray.push(...nodes)
      },
      handleContextMenu(ev, value) {
        console.log('菜单点击触发：', ev, value)

        switch (value) {
          case 'remark':
            const str = prompt('请输入备注内容：')
            if (!str) return
            graph.addRemark(str)
            break

          default:
            break
        }
      },
    },
    useThumbnail: props.useThumbnail,
    useAttrFocus: props.useAttrFocus,
    useRightMenu: props.useRightMenu,
    rightMenu,
  })
  graph.update(data)
  // graph.on('graph:zoom', (info) => console.log('graph:zoom', info))
  // graph.on('graph:move', (info) => console.log('graph:move', info))
  graph.on('contextmenu:show', (info) => {
    console.log(info,'info')
    graph.updateConfig({
      rightMenu: [{
        label: 'test',
        value: '11'
      }] 
    })
  })
  // graph.on('graph:beforeUpdate', (info) => {
  //   // console.log('graph:beforeUpdate', info)
  // })
  // graph.on('graph:afterUpdate', (info) => {
  //   // console.log('graph:beforeUpdate', info)
  // })
  // graph.on('graph:mousedown', (info) => console.log('graph:mousedown', info))
  // graph.on('graph:mouseup', (info) => console.log('graph:mouseup', info))
  // graph.on('node:move', (info) => console.log('node:move', info))
  // graph.on('node:enter', (info) => console.log('node:enter', info))
  // graph.on('node:leave', (info) => console.log('node:leave', info))
  // graph.on('node:mousedown', (info) => console.log('node:mousedown', info))
  // graph.on('node:mouseup', (info) => console.log('node:mouseup', info))
  // graph.on('line:enter', (info) => console.log('line:enter', info))
  // graph.on('line:leave', (info) => console.log('line:leave', info))
  // graph.on('line:mousedown', (info) => console.log('line:mousedown', info))
  // graph.on('line:mouseup', (info) => console.log('line:mouseup', info))
  // graph.on('mark:enter', (info) => console.log('mark:enter', info))
  // graph.on('mark:leave', (info) => console.log('mark:leave', info))
  // graph.on('mark:mousedown', (info) => console.log('mark:mousedown', info))
  // graph.on('mark:mouseup', (info) => console.log('mark:mouseup', info))
  // graph.on('note:enter', (info) => console.log('note:enter', info))
  // graph.on('note:leave', (info) => console.log('note:leave', info))
  // graph.on('note:mousedown', (info) => console.log('note:mousedown', info))
  // graph.on('note:mouseup', (info) => console.log('note:mouseup', info))
}
const unmountGraph = () => {
  graph.destroy()
}
const handleFullscreen = () => {
  graph.fullscreen()
}

const changeCanvasScale = (scaleRatio) => {
  if (state.scale > 20) state.scale += scaleRatio
  graph.zoom(state.scale)
}

const addNode = () => {
  graph.addNode({
    id: graph.createKey(),
    attributes: []
  })
}

const addLine = (lineType) => {
  graph.addLine(lineType)
}

const addMark = () => {
  graph.addMark()
}

const addNote = () => {
  graph.addNote()
}

const changeColor = (value, tp) => {
  graph.changeColor(value, tp)
}

const setEditable = () => {
  state.lock = state.lock === 1 ? 2 : 1
  graph.setEditable(state.lock)
}

const copy = () => {
  graph.copy()
}

const createLines = () => {
  const list = [
    { "from": 0, "to": 1, "pos": [{ x: 200, y: 125 }, { x: 400, y: 125 }], type: 'primaryKey', numLineTypes: 1 },
  ]
  graph.createLines(list)
}

const updateLayout = () => graph.updateLayout()

onMounted(() => {
  mountGraph()
})
onBeforeUnmount(unmountGraph)
</script>

