import { Id } from '../../../types'
import { generateTree, TreeNode } from './utils/tree'
import {
  HORIZONTAL_SPACE,
  VERTICAL_SPACE,
  FONT_SIZE,
  FONT_FAMILY,
  LINE_HEIGHT,
} from './utils/conf'
import { getAutoSizeValue } from './utils/auto-size'
interface NodeData {
  id: string
  x: number
  y: number
  width?: number
  height?: number
  bgC?: string
  bdC?: string
  name?: string
  physicalTables?: boolean
  quotePrimaryKey?: boolean
  type?: 'inclusive' | 'inside' | 'explain'
  attributes?: Attribute[]
}

interface Attribute {
  name: string
  id?: string
  primaryKey?: boolean
  foreignKey?: boolean
  type?: string
  icons?: Icon[]
  children?: Attribute[]
  label?: string
  content?: string
}

interface Icon {
  bgC?: string
  color?: string
  text?: string
  bdC?: string
}

interface LineData {
  id: number
  from: string
  to: string
  pos: Point[]
  type: string
  numLineTypes: number
  remark?: string
}

interface Point {
  x: number
  y: number
}

export function hierarchicalLayout(
  nodeDataArray: NodeData[],
  linkDataArray: LineData[],
) {
  const layers = createLayers(nodeDataArray, linkDataArray)

  layoutNodes(nodeDataArray, layers)
}

/**
 * 生成layer矩阵
 */
const createLayers = (nodes: NodeData[], lines: LineData[]) => {
  /**
   * 计算每个节点的深度和父节点
   */
  const nodeParents = new Map<Id, Set<Id>>()

  for (const node of nodes) {
    nodeParents.set(node.id, new Set())
  }

  for (const line of lines) {
    const parents = nodeParents.get(line.to)

    // 关系中包含不存在的实体不计入
    const existToNode = !!parents
    const existFromNode = !!nodeParents.get(line.from)
    if (!existFromNode || !existToNode) continue

    parents.add(line.from)
  }

  /**
   * 生成layer矩阵
   */
  const layers: TreeNode[] = []

  let allDone = false
  while (!allDone) {
    if (!nodeParents.size) {
      allDone = false
      break
    }

    let minParentsId = null
    // 每循环一次为一组，对应第一层[]
    for (const [toId, fromIds] of nodeParents) {
      // 找到父级最少的节点作为顶级节点
      const ifEmpty = minParentsId === null
      const ifLess = fromIds.size < nodeParents.get(minParentsId)?.size
      const ifMin = minParentsId === 0
      if (ifEmpty || ifLess) minParentsId = toId
      if (ifMin) break
    }

    if (!nodeParents.get(minParentsId).size) nodeParents.delete(minParentsId)

    const groupData = generateTree(nodeParents, minParentsId)
    layers.push(groupData)
  }

  return layers
}

/**
 * 根据矩阵，重新设定各个实体的x,y,w,h
 */
const layoutNodes = (nodes: NodeData[], layers: TreeNode[]) => {
  const nodeMap = new Map<Id, NodeData>()
  nodes.map((node) => nodeMap.set(node.id, node))
  console.log(layers)

  autoSize(nodes)

  layers.reduce(
    (x, group) => (x = recursionGroup(nodeMap, [group], x, 100)),
    100,
  )
}

const autoSize = (nodes: NodeData[]) => {
  nodes.map((node) => {
    if (node.type === 'inclusive') return
    const autoSizeValue = getAutoSizeValue(
      node,
      FONT_SIZE,
      FONT_FAMILY,
      LINE_HEIGHT,
    )
    node.width = autoSizeValue.width
    node.height = autoSizeValue.height
  })
}

const recursionGroup = (nodeMap, rows, x, y) => {
  // const curRowMaxWidth = Math.max(...rows.map((it) => it.width))
  const curRowMaxHeight = Math.max(...rows.map((it) => it.height || 100))
  const nextBeginY = y + curRowMaxHeight + VERTICAL_SPACE
  let nextBeginX = x,
    maxX = x
  for (let i = 0; i < rows.length; i++) {
    const row = nodeMap.get(rows[i].id)
    row.x = nextBeginX
    row.y = y
    nextBeginX += row.width + HORIZONTAL_SPACE
    maxX = Math.max(nextBeginX, maxX)
    if (rows[i].children?.length)
      maxX = Math.max(
        recursionGroup(nodeMap, rows[i].children, x, nextBeginY),
        maxX,
      )
  }
  return maxX
}
