import { EditType, Cursor, TargetDirection } from '../shared/enum'
import { NodeErwin } from '../../core/materials/node'
import { NodeRect } from '../../core/materials/node'
import { LineErwin } from '../../core/materials/line'
import { TextBase } from '../../core/materials/text'
import type { Erg } from '../index'
import { isBoolean } from 'lodash-es'
import {
  NodeEvent,
  MarkEvent,
  NoteEvent,
  GraphEvent,
  LineEvent,
} from '../shared/event'

/**
 * 是否到达八点框
 */
const ifAnchor = (target: unknown, canvasEv) => {
  const ifLegalMaterials =
    target instanceof NodeErwin || target instanceof NodeRect
  if (!ifLegalMaterials) return false

  // 是否八点框触发
  const { x, y, width, height } = target
  const middleX = x + width / 2
  const middleY = y + height / 2
  const fullX = x + width
  const fullY = y + height
  const tolerance = 5
  let targetDirection = TargetDirection.NONE

  if (
    Math.abs(x - canvasEv.x) <= tolerance &&
    Math.abs(y - canvasEv.y) <= tolerance
  )
    targetDirection = TargetDirection.LT
  else if (
    Math.abs(middleX - canvasEv.x) <= tolerance &&
    Math.abs(y - canvasEv.y) <= tolerance
  )
    targetDirection = TargetDirection.TM
  else if (
    Math.abs(fullX - canvasEv.x) <= tolerance &&
    Math.abs(y - canvasEv.y) <= tolerance
  )
    targetDirection = TargetDirection.RT
  else if (
    Math.abs(x - canvasEv.x) <= tolerance &&
    Math.abs(middleY - canvasEv.y) <= tolerance
  )
    targetDirection = TargetDirection.LM
  else if (
    Math.abs(fullX - canvasEv.x) <= tolerance &&
    Math.abs(middleY - canvasEv.y) <= tolerance
  )
    targetDirection = TargetDirection.RM
  else if (
    Math.abs(x - canvasEv.x) <= tolerance &&
    Math.abs(fullY - canvasEv.y) <= tolerance
  )
    targetDirection = TargetDirection.LB
  else if (
    Math.abs(middleX - canvasEv.x) <= tolerance &&
    Math.abs(fullY - canvasEv.y) <= tolerance
  )
    targetDirection = TargetDirection.BM
  else if (
    Math.abs(fullX - canvasEv.x) <= tolerance &&
    Math.abs(fullY - canvasEv.y) <= tolerance
  )
    targetDirection = TargetDirection.RB

  return targetDirection
}

export const useMouseMove = function (this: Erg, e, canvasEv) {
  /**
   * 移动备注框
   */
  const moveMarkRect = () => {
    const { eventObj } = this
    const offsetOfMove = {
      x: canvasEv.x - eventObj.targetOffsetX - eventObj.target.x,
      y: canvasEv.y - eventObj.targetOffsetY - eventObj.target.y,
    }
    const newLoc = {
      x: eventObj.target.x + offsetOfMove.x,
      y: eventObj.target.y + offsetOfMove.y,
    }

    this.markMap.get(eventObj.target.id).updateOptions(newLoc)
    this.emit(MarkEvent.Move, this.markMap.get(eventObj.target.id))
    this.update()
  }

  /**
   * 移动备注文字
   */
  const moveMarkText = () => {
    const { eventObj } = this

    const offsetOfMove = {
      x: canvasEv.x - eventObj.targetOffsetX - eventObj.target.x,
      y: canvasEv.y - eventObj.targetOffsetY - eventObj.target.y,
    }

    const newLoc = {
      x: eventObj.target.x + offsetOfMove.x,
      y: eventObj.target.y + offsetOfMove.y,
    }
    this.noteMap.get(eventObj.target.id).updateOptions(newLoc)

    this.emit(NoteEvent.Move, eventObj.target)
    this.update()
  }

  /**
   * 修改实体尺寸
   */
  const resizeNode = () => {
    const { eventObj } = this
    // eventObj.target.manualChange = true
    const { x, y } = canvasEv
    if (eventObj.target.type === 'mark') {
      this.resizeNode(x, y, eventObj, null)
    } else {
      this.resizeNode(x, y, eventObj)
    }

    this.update()
  }

  /**
   * 移动实体
   */
  const moveNode = () => {
    const { focusTarget, eventObj, nodeRelatedLineMap } = this

    const offsetOfMove = {
      x: canvasEv.x - eventObj.targetOffsetX - eventObj.target.x,
      y: canvasEv.y - eventObj.targetOffsetY - eventObj.target.y,
    }

    focusTarget.forEach((focusItem) => {
      if (!(focusItem instanceof NodeErwin)) return

      const nodeNewLoc = {
        x: focusItem.x + offsetOfMove.x,
        y: focusItem.y + offsetOfMove.y,
      }

      // 节点相关线段移动
      if (nodeRelatedLineMap.has(focusItem.id)) {
        const involveLines = [
          ...nodeRelatedLineMap.get(focusItem.id).from,
          ...nodeRelatedLineMap.get(focusItem.id).to,
        ]
        involveLines.forEach((line) => {
          line.from === line.to
            ? this.moveLineForSameNode(line, { target: focusItem })
            : this.moveLineForNode(line, { target: focusItem }, nodeNewLoc)
        })
      }

      this.nodeMap.get(focusItem.id).updateOptions(nodeNewLoc)
      this.emit(NodeEvent.Move, focusItem)
    })

    this.update()
  }

  /**
   * 移动属性
   */
  const moveAttr = () => {
    console.log('attr')
    const { eventObj } = this
    const sourceNode = eventObj.target
    const sourceAttrId = eventObj.targetAttrId
    const targetNode = null
    const targetAttrId = ''
    this.updateEventCanvas()
  }

  /**
   * 移动连线
   */
  const moveLine = () => {
    const { eventObj } = this

    const idx = eventObj.target.getIndexInPos(eventObj.lastEvLoc)
    eventObj.targetLineStartPointIdx = idx
    eventObj.lastEvLoc = canvasEv
    if (idx === false) return
    this.moveLineForLine(eventObj, canvasEv)
    this.update()
  }

  /**
   * 移动画布
   */
  const moveCanvas = () => {
    this.canvas.setTranslate(e)
    this.canvas.clearEventCanvas()
    const graphTranslate = this.canvas.getTranslate()
    this.emit(GraphEvent.Move, graphTranslate)
    this.update()
  }

  /**
   * 改变指针样式
   */
  const changeMouseCursor = () => {
    const { editType, renderElements } = this

    const cursorMap = {
      [EditType.addNode]: Cursor.cell,
      [EditType.primaryKey]: Cursor.crosshair,
      [EditType.foreignKey]: Cursor.crosshair,
      [EditType.branchLine]: Cursor.crosshair,
      [EditType.addMark]: Cursor.cell,
      [EditType.addNote]: Cursor.text,
      [EditType.copy]: Cursor.copy,
    }

    const cursorState = cursorMap[editType]
    // 有指定操作：
    if (cursorState)
      return (this.canvas.eventEl.style.cursor = cursorState || Cursor.default)

    let hoverTarget = null,
      cursor = Cursor.grab

    // 无指定操作：到达热点位置变换鼠标状态
    for (let i = renderElements.length - 1; i >= 0; i--) {
      const el = renderElements[i]
      const isTarget = el.containsPoint(canvasEv) || ifAnchor(el, canvasEv)
      if (isTarget) {
        hoverTarget = el

        cursor =
          isTarget === TargetDirection.LT
            ? Cursor.nwseResize
            : isTarget === TargetDirection.TM
            ? Cursor.nsResize
            : isTarget === TargetDirection.RT
            ? Cursor.neswResize
            : isTarget === TargetDirection.LM
            ? Cursor.ewResize
            : isTarget === TargetDirection.RM
            ? Cursor.ewResize
            : isTarget === TargetDirection.LB
            ? Cursor.neswResize
            : isTarget === TargetDirection.BM
            ? Cursor.nsResize
            : isTarget === TargetDirection.RB
            ? Cursor.nwseResize
            : Cursor.move
        break
      }
    }

    // 触发通知
    if (this.hoverTarget !== hoverTarget) {
      const OldTargetEvent =
        this.hoverTarget instanceof NodeErwin
          ? NodeEvent
          : this.hoverTarget instanceof LineErwin
          ? LineEvent
          : this.hoverTarget instanceof NodeRect
          ? MarkEvent
          : this.hoverTarget instanceof TextBase
          ? NoteEvent
          : null
      const NewTargetEvent =
        hoverTarget instanceof NodeErwin
          ? NodeEvent
          : hoverTarget instanceof LineErwin
          ? LineEvent
          : hoverTarget instanceof NodeRect
          ? MarkEvent
          : hoverTarget instanceof TextBase
          ? NoteEvent
          : null
      OldTargetEvent && this.emit(OldTargetEvent.Leave, this.hoverTarget)
      NewTargetEvent && this.emit(NewTargetEvent.Enter, hoverTarget)
    }

    // 无指定操作：未到达热点位置
    this.hoverTarget = hoverTarget
    this.canvas.eventEl.style.cursor = cursor
    this.updateEventCanvas()
  }

  /**
   * 移动元素
   */
  const moveElement = () => {
    const { eventObj } = this

    const isAttrMove = eventObj.targetAttrId
    const isNodeResize = !!eventObj.targetDirection
    const isNodeMove = eventObj.target instanceof NodeErwin
    const isLineMove = eventObj.target instanceof LineErwin
    const isMarkMove = eventObj.target instanceof NodeRect
    const isNoteMove = eventObj.target instanceof TextBase

    if (isAttrMove) return moveAttr()
    if (isNodeResize) return resizeNode()
    if (isNodeMove) return moveNode()
    if (isLineMove) return moveLine()
    if (isMarkMove) return moveMarkRect()
    if (isNoteMove) return moveMarkText()
  }

  return {
    changeMouseCursor,
    moveCanvas,
    moveElement,
  }
}

export const useMouseDown = function (this: Erg, e, canvasEv) {
  const { x: evX, y: evY } = canvasEv

  const execTask = () => {
    const { editType, defaultWidth, defaultHeight } = this
    switch (editType) {
      case EditType.copy: {
        const distanse = {
          x: evX - this.copyTarget?.[0]?.x,
          y: evY - this.copyTarget?.[0]?.y,
        }

        const fixLocNodes = this.copyTarget
          .filter((it) => !!it.attributes)
          .map((node) => {
            node.x = node.x + distanse.x
            node.y = node.y + distanse.y
            return node
          })

        // eslint-disable-next-line @typescript-eslint/ban-ts-comment
        // @ts-ignore
        this.externalFn?.handlePaste(fixLocNodes)
        this.editType = EditType.None
        this.canvas.eventEl.style.cursor = Cursor.default
        this.update()
        break
      }
      case EditType.addNode: {
        const newNode = {
          x: evX - defaultWidth / 2,
          y: evY - defaultHeight / 2,
          width: defaultWidth,
          height: defaultHeight,
        }

        // eslint-disable-next-line @typescript-eslint/ban-ts-comment
        // @ts-ignore
        this.externalFn.addNode(newNode)
        this.editType = EditType.None
        this.canvas.eventEl.style.cursor = Cursor.default
        this.update()
        break
      }
      case EditType.addMark: {
        const newMark = {
          id: this.createKey(),
          x: evX,
          y: evY,
          width: defaultWidth,
          height: defaultHeight,
          type: 'mark',
        }

        // eslint-disable-next-line @typescript-eslint/ban-ts-comment
        // @ts-ignore
        this.externalFn.addMark(newMark)
        this.editType = EditType.None
        this.canvas.eventEl.style.cursor = Cursor.default
        this.update()
        break
      }
      case EditType.addNote: {
        const newNote = {
          id: this.createKey(),
          x: evX,
          y: evY,
          text: '',
          type: 'note',
        }

        // eslint-disable-next-line @typescript-eslint/ban-ts-comment
        // @ts-ignore
        this.externalFn.addNote(newNote)
        this.editType = EditType.None
        this.canvas.eventEl.style.cursor = Cursor.default
        break
      }
      // case EditType.primaryKey:

      //   break
      // case EditType.foreignKey:

      //   break
      // case EditType.branchLine:

      //   break

      default:
        break
    }
  }

  const setEventTarget = (e: MouseEvent) => {
    const { metaKey, ctrlKey } = e
    const { hoverTarget, focusTarget, eventObj } = this

    eventObj.target = hoverTarget
    this.canvas.eventEl.style.cursor = Cursor.grabbing

    if (!hoverTarget) {
      focusTarget.clear()
      this.updateEventCanvas()
      return
    }

    // 触发外部事件
    const TargetEvent =
      hoverTarget instanceof NodeErwin
        ? NodeEvent
        : hoverTarget instanceof LineErwin
        ? LineEvent
        : hoverTarget instanceof NodeRect
        ? MarkEvent
        : hoverTarget instanceof TextBase
        ? NoteEvent
        : null
    this.emit(TargetEvent.MouseDown, hoverTarget)

    const targetDirection = ifAnchor(hoverTarget, canvasEv)

    // 处理实体属性聚焦：只有已聚焦的实体才会触发内部的属性聚焦
    const attrId = hoverTarget.containsPoint(canvasEv)
    const ifAttrFocus =
      hoverTarget instanceof NodeErwin &&
      focusTarget.has(hoverTarget) &&
      !targetDirection &&
      !isBoolean(attrId)
    if (ifAttrFocus) return (eventObj.targetAttrId = attrId)

    // 未多选 -> 清空聚焦
    if (!ctrlKey && !metaKey) focusTarget.clear()

    // 处理物料聚焦
    if (hoverTarget instanceof LineErwin) {
      focusTarget.clear()
      eventObj.lastEvLoc = canvasEv
    } else if (
      hoverTarget instanceof NodeErwin ||
      hoverTarget instanceof TextBase ||
      hoverTarget instanceof NodeRect
    ) {
      eventObj.targetOffsetX = canvasEv.x - hoverTarget.x
      eventObj.targetOffsetY = canvasEv.y - hoverTarget.y
      eventObj.targetDirection = targetDirection || null
    }
    focusTarget.add(hoverTarget)

    this.updateEventCanvas()
  }

  return {
    execTask,
    setEventTarget,
  }
}

export const useMouseUp = function (this: Erg) {
  const resetMouseState = () => {
    const { hoverTarget } = this
    // 触发外部事件
    const TargetEvent =
      hoverTarget instanceof NodeErwin
        ? NodeEvent
        : hoverTarget instanceof LineErwin
        ? LineEvent
        : hoverTarget instanceof NodeRect
        ? MarkEvent
        : hoverTarget instanceof TextBase
        ? NoteEvent
        : null
    TargetEvent && this.emit(TargetEvent.MouseUp, hoverTarget)

    this.canvas.eventEl.style.cursor = Cursor.default
    this.eventObj = {
      target: null,
      targetAttrId: null,
      targetDirection: TargetDirection.NONE,
      targetOffsetX: 0,
      targetOffsetY: 0,
      targetLineStartPointIdx: 0,
      lastEvLoc: { x: 0, y: 0 },
    }
  }
  return {
    resetMouseState,
  }
}
