import type { Erg } from '..'
import { Id } from '../../types'

interface DataHandlerOptions {
  context?: Erg
  data: {
    nodeDataArray: []
    linkDataArray: []
    markDataArray: []
    noteDataArray: []
  }
}

/**
 * 可插拔的数据更新系统
 *  - 调用此类的mountAutoUpdate -> 渲染器直接修改原始数据
 *  - 不调用此类mountAutoUpdate -> 渲染器不修改原始数据，如需正确渲染，需要用户外部手动挂载监听修改数据
 */
export class DataHandler {
  private context: Erg
  public originData = {
    nodeDataArray: [],
    linkDataArray: [],
    markDataArray: [],
    noteDataArray: [],
  }
  public originDataMap: Map<Id, unknown> = new Map()

  constructor(options: DataHandlerOptions) {
    this.update(options)
    this.mountAutoUpdate()
  }

  public update(options: DataHandlerOptions) {
    this.analysisOptions(options)
  }

  private analysisOptions(options: DataHandlerOptions) {
    const {
      data = {
        nodeDataArray: [],
        linkDataArray: [],
        markDataArray: [],
        noteDataArray: [],
      },
      context,
    } = options

    if (context) this.context = context
    this.originData = data

    this.initMap()
  }

  private initMap() {
    const {
      originData: {
        nodeDataArray = [],
        linkDataArray = [],
        markDataArray = [],
        noteDataArray = [],
      },
      originDataMap,
    } = this

    originDataMap.clear()
    for (let i = 0; i < nodeDataArray.length; i++) {
      const el = nodeDataArray[i]
      originDataMap.set(el.id, el)
    }
    for (let i = 0; i < linkDataArray.length; i++) {
      const el = linkDataArray[i]
      originDataMap.set(el.id, el)
    }
    for (let i = 0; i < markDataArray.length; i++) {
      const el = markDataArray[i]
      originDataMap.set(el.id, el)
    }
    for (let i = 0; i < noteDataArray.length; i++) {
      const el = noteDataArray[i]
      originDataMap.set(el.id, el)
    }
  }

  private mountAutoUpdate() {
    const { originDataMap, context } = this
    context.on('node:move', (node) => {
      const it = originDataMap.get(node.id)
      it.x = node.x
      it.y = node.y
      it.width = node.width
      it.height = node.height
    })
    context.on('node:resize', (node) => {
      const it = originDataMap.get(node.id)
      it.x = node.x
      it.y = node.y
      it.width = node.width
      it.height = node.height
    })
    context.on('line:move', (line) => {
      const it = originDataMap.get(line.id)
      it.pos = line.pos
    })
    context.on('note:move', (note) => {
      const it = originDataMap.get(note.id)
      it.x = note.x
      it.y = note.y
    })
    context.on('mark:move', (node) => {
      const it = originDataMap.get(node.id)
      it.x = node.x
      it.y = node.y
      it.width = node.width
      it.height = node.height
    })
    context.on('mark:resize', (node) => {
      const it = originDataMap.get(node.id)
      it.x = node.x
      it.y = node.y
      it.width = node.width
      it.height = node.height
    })
    // graph.on('line:click', (id) => console.log('line:click', id))
    // graph.on('node:click', (id) => console.log('node:click', id))
    // graph.on('node-attr:click', (id) => console.log('node-attr:click', id))
  }

  private unmountAutoUpdate() {
    const { context } = this
    context.off()
  }

  private freeVariable() {
    this.originData = null
    this.originDataMap = null
  }

  public destroy() {
    this.unmountAutoUpdate()
    this.freeVariable()
  }
}
